import RegionInfoCallInterface from "./RegionInfoCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiRegionInfoInterface from "./EsiRegionInfoInterface";
import EsiError from "../../../error/EsiError/EsiError";

class RegionInfoCall implements RegionInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(id: number): Promise<EsiRegionInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v1/universe/regions/${id}/`;
  }
}

export default RegionInfoCall;
