import EntityManagerInterface from "./EntityManagerInterface";
import MigrationInterface from "./Migration/MigrationInterface";

class EntityManager implements EntityManagerInterface {
  private _migrations: MigrationInterface[]

  constructor(
    migrations: MigrationInterface[]
  ) {
    this._migrations = migrations;
  }

  public async getConnection(): Promise<IDBDatabase> {
    let dbName = "evelook";
    let versions = [0, 1];

    return new Promise((resolve, reject) => {
      let dbReq = indexedDB.open(dbName, 1);

      dbReq.onupgradeneeded = event => {
        versions.forEach(version => {
          if (event.oldVersion < version) {
            this._migrations.forEach(migration => {
              migration.run(dbReq.result, version);
            });
          }
        });
      };

      dbReq.onsuccess = () => {
        resolve(dbReq.result);

        return;
      };

      dbReq.onblocked = () => {
        reject("Database version can't be upgraded because the app is open somewhere else.");
      }

      dbReq.onerror = () => {
        let error = dbReq.error;
        let message = "Error opening database";
        if (error !== null) {
          message += `: ${error.code} ${error.message} ${error.name}`;
        }
        reject(message);

        return;
      };
    });
  }
}

export default EntityManager;
