import StationInfoCallInterface from "./StationInfoCallInterface";
import EsiStationInfoInterface from "./EsiStationInfoInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";

class StationInfoCall implements StationInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  public async get(id: number): Promise<EsiStationInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v2/universe/stations/${id}/`;
  }
}

export default StationInfoCall;
