import EsiClientInterface from "./EsiClientInterface";
import EsiErrorManagerInterface from "../EsiErrorManager/EsiErrorManagerInterface";
import Query from "../QueryFactory/Query";

class EsiClient implements EsiClientInterface {
  public readonly baseUrl = "https://esi.evetech.net";
  private readonly _userAgent = "evelook";
  private readonly _fetchClient;
  private readonly _esiErrorManager;

  constructor(
    fetchClient: (
      input: RequestInfo,
      init?: RequestInit
    ) => Promise<Response>,
    esiErrorManager: EsiErrorManagerInterface
  ) {
    this._fetchClient = fetchClient;
    this._esiErrorManager = esiErrorManager;
  }

  /**
   * @throws Error On network error or broken error window.
   */
  public async getResponse(
    endUrl: string,
    method: "GET" | "POST" | "DELETE" | "PUT",
    query: Query | null = null,
    body: object | null = null
  ): Promise<Response> {
    if (this._esiErrorManager.isErrorWindowBroken()) {
      throw new Error("Aborting, too many errors.");
    }

    let headers = {
      "X-User-Agent": this._userAgent,
    };

    let options: RequestInit = {
      method: method,
      cache: "default",
      headers: headers
    };

    let uri = `${this.baseUrl}${endUrl}`;
    if (query !== null && !query.isEmpty()) {
      uri = uri.concat(`?${query.toString()}`);
    }

    if (body !== null) {
      options["body"] = JSON.stringify(body);
    }

    let response = await this._fetchClient(uri, options);
    this._esiErrorManager.saveErrorWindowInfo(response.headers);
    if (!response.ok) {
      if (response.status === 420) {  // We are error limited
        this._esiErrorManager.flagWindowAsBroken();
      } else {
        this._esiErrorManager.incrementError();
      }
    }

    return response;
  }
}

export default EsiClient;
