import SystemInfoCallInterface from "./SystemInfoCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiSystemInfoInterface from "./EsiSystemInfoInterface";
import EsiError from "../../../error/EsiError/EsiError";

class SystemInfoCall implements SystemInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(id: number): Promise<EsiSystemInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v4/universe/systems/${id}/`;
  }
}

export default SystemInfoCall;
