import SearchRepositoryInterface from "./SearchRepositoryInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import PublicSearchCallInterface from "../../service/Call/PublicSearchCall/PublicSearchCallInterface";
import RepoCategory from "./RepoCategory";
import EsiCategory, {esiCategories} from "../../service/Call/PublicSearchCall/EsiCategory";
import EsiUniverseNamesInterface from "../../service/Call/UniverseNamesCall/EsiUniverseNamesInterface";
import ResultWithLinkInterface from "./ResulWithLinkInterface";
import {MutableRefObject} from "react";


export type AutocompleteCache = { [key: string]: ResultWithLinkInterface[] };

class SearchRepository implements SearchRepositoryInterface {
  private readonly _nameRepo;
  private readonly _publicSearchCall;

  constructor(
    nameRepo: NameRepositoryInterface,
    publicSearchCall: PublicSearchCallInterface
  ) {
    this._nameRepo = nameRepo;
    this._publicSearchCall = publicSearchCall;
  }

  /**
   * @throws Error
   */
  public async publicSearch(
    searchString: string,
    category: RepoCategory,
    strict: boolean
  ): Promise<ResultWithLinkInterface[]> {
    let categoriesForCall: EsiCategory[];
    if (category === "all") {
      categoriesForCall = esiCategories;
    } else {
      categoriesForCall = [category];
    }
    let search = await this._publicSearchCall.get(searchString, categoriesForCall, strict);

    let ids: number[] = [];
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    for (let [key, value] of Object.entries(search)) {
      ids = ids.concat(value);
    }
    let names = await this._nameRepo.resolveGeneric(ids);

    return this._composeResult(names);
  }

  public async forAutocomplete(
    searchString: string,
    category: RepoCategory,
    cacheRef: MutableRefObject<AutocompleteCache>
  ): Promise<ResultWithLinkInterface[]> {
    console.log(cacheRef.current);
    if (cacheRef.current.hasOwnProperty(searchString)) {
      return cacheRef.current[searchString];
    }

    let categoriesForCall: EsiCategory[];
    if (category === "all") {
      categoriesForCall = esiCategories;
    } else {
      categoriesForCall = [category];
    }
    let search = await this._publicSearchCall.get(searchString, categoriesForCall, false);

    let ids: number[] = [];
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    for (let [key, value] of Object.entries(search)) {
      ids = ids.concat(value);
    }

    let uniNames = await this._nameRepo.resolveGeneric(ids);
    let justNames: EsiUniverseNamesInterface[] = [];
    let lowercaseSearchString = searchString.toLowerCase();
    uniNames.forEach(uniName => {
      let { name } = uniName;
      if (name.toLowerCase().startsWith(lowercaseSearchString)) {
        justNames.push(uniName);
      }
    });

    let orderedNames = justNames.sort((a, b) => a.name.length - b.name.length);
    let result = this._composeResult(orderedNames);

    // Safety net to prevent the cache to grow indefinitely.
    if (Object.keys(cacheRef.current).length > 30) {
      cacheRef.current = {};
    }

    cacheRef.current = {
      ...cacheRef.current,
      [searchString]: result
    };

    return result;
  }

  private _composeResult(names: EsiUniverseNamesInterface[]): ResultWithLinkInterface[] {
    let result: ResultWithLinkInterface[] = [];
    names.forEach(name => {
      let prettyCategory;
      let link;
      switch (name.category) {
        case "alliance":
          prettyCategory = "Alliance";
          link = "/alliance/";
          break;
        case "character":
          prettyCategory = "Character";
          link = "/character/";
          break;
        case "constellation":
          prettyCategory = "Constellation";
          //link = "/constellation/";
          link = null;
          break;
        case "corporation":
          prettyCategory = "Corporation";
          link = "/corporation/";
          break;
        case "inventory_type":
          prettyCategory = "Item";
          //link = "/type/";
          link = null;
          break;
        case "region":
          prettyCategory = "Region";
          //link = "/region/";
          link = null;
          break;
        case "solar_system":
          prettyCategory = "System";
          //link = "/system/";
          link = null;
          break;
        case "station":
          prettyCategory = "Station";
          //link = "/station/";
          link = null
          break;
        case "faction":
          prettyCategory = "Faction";
          //link = "/faction/";
          link = null;
          break;
        default:
          prettyCategory = name.category;
          link = null;
      }

      result.push({
        ...name,
        prettyCategory: prettyCategory,
        link: link
      });
    });

    return result;
  }
}

export default SearchRepository;
