import React, {useEffect, useState} from 'react';
import RSSFeedEntryInterface from "../../../repository/RSSRepository/RSSFeedEntryInterface";
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import Paper from '@material-ui/core/Paper';
import {makeStyles, Theme, createStyles} from '@material-ui/core/styles';
import RSSContent from "../RSSContent/RSSContent";
import RSSSkeleton from "../RSSSkeleton/RSSSkeleton";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonLinkParent: {
      right: 0,
    },
    cardTitle: {
      paddingTop: theme.spacing(2),
      paddingLeft: theme.spacing(3)
    },
    gridItem: {
      [theme.breakpoints.down('xs')]: {
        paddingTop: theme.spacing(3),
      }
    },
  }),
);

export default function RSSPaper(
  {
    title,
    source
  }: {
    title: string,
    source: () => Promise<RSSFeedEntryInterface[]>
  }
) {
  const classes = useStyles();
  const [paperContent, setPaperContent] = useState<React.ReactElement | null>(<RSSSkeleton />);

  useEffect(() => {
    source()
      .then(entries => setPaperContent(<RSSContent entries={entries} />))
      .catch(e => console.log(e));
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12} md={4} className={classes.gridItem}>
        <Paper>
          <Typography variant="h6" className={classes.cardTitle}>
            {title}
          </Typography>
          <List dense={true}>
            {paperContent}
          </List>
        </Paper>
      </Grid>
    </React.Fragment>
  );
}
