import NameRepositoryInterface from "./NameRepositoryInterface";
import UniverseNamesCallInterface from "../../service/Call/UniverseNamesCall/UniverseNamesCallInterface";
import EsiUniverseNamesInterface from "../../service/Call/UniverseNamesCall/EsiUniverseNamesInterface";

class NameRepository implements NameRepositoryInterface {
  private readonly _universeNamesCaller;

  constructor(universeNamesCaller: UniverseNamesCallInterface) {
    this._universeNamesCaller = universeNamesCaller;
  }

  public async resolveGeneric(ids: number[]): Promise<EsiUniverseNamesInterface[]> {
    this._removeBannedIDs(ids);

    let allResolvedIDs: EsiUniverseNamesInterface[] = [];
    let failuresCount = 0;
    let maxAllowedFailures = 20;

    let chunks: number[][] = [];
    while (ids.length > 0) {
      chunks.push(ids.splice(0, 999));
    }

    for (let chunk of chunks) {
      if (failuresCount >= maxAllowedFailures) {
        throw new Error("");  // TODO
      }

      try {
        let localResolvedIDs = await this._universeNamesCaller.get(chunk);
        allResolvedIDs = allResolvedIDs.concat(localResolvedIDs);
      } catch (e) {
        failuresCount++;

        if (chunk.length > 1) {
          // We split the chunk in half for retries
          chunks.push(chunk.splice(0, Math.round(chunk.length / 2)));
          chunks.push(chunk);
        }
      }
    }

    return allResolvedIDs;
  }

  private _removeBannedIDs(ids: number[]): void {
    let index = ids.indexOf(1);
    if (index !== -1) {
      ids.splice(index, 1);
    }
  }
}

export default NameRepository;
