import FactionCallInterface from "./FactionCallInterface";
import EsiFactionInterface from "./EsiFactionInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";

class FactionCall implements FactionCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(): Promise<EsiFactionInterface[]> {
    let response = await this._esiClient.getResponse(this._getUrl(), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(): string {
    return `/v2/universe/factions/`;
  }
}

export default FactionCall;
