import MigrationInterface from "./MigrationInterface";

class MarketOrdersMigration implements MigrationInterface {
  public run(db: IDBDatabase, version: number): void {
    if (version < 1) {
      let store = db.createObjectStore(
        "market_orders",
        {
          keyPath: "order_id",
          autoIncrement: false
        }
      );
      store.createIndex("by_region", "region_id");
    }
  }
}

export default MarketOrdersMigration;
