import RSSRepositoryInterface from "./RSSRepositoryInterface";
import RSSFeedEntryInterface from "./RSSFeedEntryInterface";

class RSSRepository implements RSSRepositoryInterface {
  public async getEveNewsFeed(): Promise<RSSFeedEntryInterface[]> {
    return this._fetch("https://www.eveonline.com/rss/json/news");
  }

  public async getDevBlogsFeed(): Promise<RSSFeedEntryInterface[]> {
    return this._fetch("https://www.eveonline.com/rss/json/dev-blogs");
  }

  public async getPatchNotesFeed(): Promise<RSSFeedEntryInterface[]> {
    return this._fetch("https://www.eveonline.com/rss/json/patch-notes");
  }

  private async _fetch(url: string): Promise<RSSFeedEntryInterface[]> {
    let response = await fetch(
      url,
      {
        cache: "default",
      }
    );

    return response.json();
  }
}

export default RSSRepository;
