import React from 'react';
import MUIDataTable, {MUIDataTableColumnDef, MUIDataTableOptions} from "mui-datatables";
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import ResultWithLinkInterface from "../../../repository/SearchRepository/ResulWithLinkInterface";
import { Link } from "react-router-dom";
import IconButton from '@material-ui/core/IconButton';
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    arrowRightIconStyle: {
      verticalAlign: "middle",
    },
    emptyIconCell: {
      minHeight: "30px"
    }
  }),
);

export default function ResultTable(
  {
    data
  }: {
    data: ResultWithLinkInterface[]
  }
) {
  const theme = useTheme();
  let classes = useStyles();
  const denseTable = useMediaQuery(theme.breakpoints.up('sm'));

  let tableOptions: MUIDataTableOptions = {
    download: false,
    elevation: 1,
    filterType: "checkbox",
    print: false,
    responsive: "standard",
    rowsPerPage: 25,
    rowsPerPageOptions: [],
    search: false,
    selectableRows: "none",
    setTableProps: () => {
      return {
        size: denseTable ? "small" : "medium"
      };
    },
    viewColumns: false
  };

  const columns: MUIDataTableColumnDef[] = [
    {
      name: "name",
      label: "Name",
      options: {
        filter: false,
        sort: true,
      }
    },
    {
      name: "prettyCategory",
      label: "Category",
      options: {
        filter: true,
        sort: true,
      }
    },
    {
      name: "link",
      options: {
        filter: false,
        sort: false,
        display: false,
      }
    },
    {
      name: "id",
      options: {
        filter: false,
        sort: false,
        display: false,
      }
    },
    {
      name: "icon",
      label: "",
      options: {
        customHeadLabelRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let link = tableMeta.rowData[2];
          let id = tableMeta.rowData[3];

          if (link === null) {
            return <div className={classes.emptyIconCell} />;
          }

          return (
            <IconButton
              size="small"
              aria-label="open"
              component={Link}
              to={`${link}${id}`}
            >
              <ArrowRightIcon className={classes.arrowRightIconStyle}/>
            </IconButton>

          );
        },
        filter: false,
        setCellProps: () => ({ style: { width: "0"}}),
        sort: false,
      }
    }
  ];

  return (
    <MUIDataTable
      title={""}
      data={data}
      columns={columns}
      options={tableOptions}
    />
  );
}
