import React, {useRef, useState, useEffect} from "react";
import {createStyles, makeStyles} from "@material-ui/core/styles";
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
import {AutocompleteInputChangeReason} from "@material-ui/lab/useAutocomplete/useAutocomplete";
import { useHistory } from "react-router-dom";
import Popper from "@material-ui/core/Popper";

import { useDeps } from "../../../context/DepsContext/DepsContext";
import SearchRepositoryInterface from "../../../repository/SearchRepository/SearchRepositoryInterface";
import ResultWithLinkInterface from "../../../repository/SearchRepository/ResulWithLinkInterface";
import AutocompleteOption from "../AutocompleteOption/AutocompleteOption.";
import AutocompleteTextField from "../AutocompleteTextField/AutocompleteTextField";
import StatusRepositoryInterface from "../../../repository/StatusRepository/StatusRepositoryInterface";
import {AutocompleteCache} from "../../../repository/SearchRepository/SearchRepository";


const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      padding: 0,
      margin: 0,
      "& li": {
        padding: 0,
        width: "100%",
      }
    },
    popper: {
      zIndex: "unset",
    }
  })
);

interface FakeResult {
  name: string,
  prettyCategory: "search...",
  link: string | null
}

export type OptionType = ResultWithLinkInterface | FakeResult;

const createLink = (option: OptionType) => {
  if ("id" in option && option.link !== null) {
    return `${option.link}${option.id}`;
  }

  let category = "all";
  if ("category" in option) {
    category = option.category;
  }

  return `/search?category=${category}&strict=false&needle=${option.name}`
};

export default function HomeSearch() {
  let history = useHistory();
  let classes = useStyles();
  let autocompleteCache = useRef<AutocompleteCache>({});
  // The options going fueling the autocomplete
  let [searchOptions, setSearchOptions] = useState<OptionType[]>([]);
  // The options just as returned from the API
  let [apiOptions, setApiOptions] = useState<Array<ResultWithLinkInterface>>([]);
  let [inputValue, setInputValue] = useState<string>("");
  let [isEveOnline, setIsEveOnline] = useState<boolean | null>(null);
  let [searchInProgress, setSearchInProgress] = useState<boolean>(false);
  let [searchTextErrorText, setSearchTextErrorText] = useState("");
  let [searchTextIsError, setSearchTextIsError] = useState(false);
  let searchTextRef = useRef<HTMLInputElement | null>(null); // FIXME: maybe it's not needed?
  let {
    searchRepository,
    statusRepository
  }: {
    searchRepository: SearchRepositoryInterface,
    statusRepository: StatusRepositoryInterface
  } = useDeps();
  const filterOptions = createFilterOptions<OptionType>({
    limit: 9,
  });

  // We do this in order to add the user input as a first option,
  // on top of the api result.
  useEffect(() => {
    if (inputValue.length < 3) {
      setSearchOptions([]);

      return;
    }

    let inputOption: FakeResult = {
      name: inputValue,
      prettyCategory: "search...",
      link: null
    };

    setSearchOptions([
      inputOption,
      ...apiOptions
    ]);
  }, [apiOptions, inputValue]);

  // reason = 'input' on manual typing
  // reason = 'reset' after click on suggestion
  const handleInputChange = (
    event: React.ChangeEvent<{}>,
    value: string,
    reason: AutocompleteInputChangeReason
  ): void => {
    setInputValue(value);

    // When the text box gets cleared with the "x" or when
    // the user backspace without triggering new searches
    if (value.length < 3) {
      setApiOptions([]);

      return;
    }

    if (
      searchInProgress
      || isEveOnline === false
      || reason === "reset"
      || value.length < 3
      || value.length % 2 === 0
    ) {
      return;
    }

    setSearchInProgress(true);
    searchRepository.forAutocomplete(value, "all", autocompleteCache)
      .then(names => {
        setApiOptions(names);
      })
      .catch(e => {

      })
      .finally(() => {
        setSearchInProgress(false);
      });
  };

  const handleSelect = (
    event: React.ChangeEvent<{}>,
    value: string | OptionType,  // no idea where the string type come from, possibly it's the string the user has typed without selecting anything from the box
    reason: string
  ): void => {
    if (typeof value === 'string') {
      return;
    }

    event.preventDefault();
    history.push(createLink(value));
  }

  // Handle a submit that didn't involved the selection box, so only triggered
  // by pressing enter while in the text field, or by clicking the search button.
  const handleSubmitWithoutOption = (): void => {
    if (inputValue.length < 3) {
      setSearchTextIsError(true);
      setSearchTextErrorText("It must be at least 3 characters long.")

      return;
    }

    history.push(`/search?category=all&strict=false&needle=${inputValue}`);
  }

  const handleKeyPress = (e: React.KeyboardEvent): void => {
    if (e.key === "Enter") {
      handleSubmitWithoutOption();
    }
  }

  const handleFocus = (): void => {
    if (isEveOnline !== null) {
      return;
    }

    statusRepository.isEveOnline()
      .then(result => setIsEveOnline(result))
      .catch(e => console.error(e));
  }

  return (
    <React.Fragment>
      <Autocomplete
        disableClearable
        filterOptions={filterOptions}
        freeSolo  // The user input is not bound to provided options.
        getOptionLabel={(option: OptionType | string) => typeof option === 'string' ? option : option.name}
        id="free-solo-2-demo"
        ListboxProps={{
          className: [classes.root],
        }}
        onFocus={handleFocus}
        onChange={handleSelect}
        onInputChange={handleInputChange}
        onKeyPress={handleKeyPress}
        options={searchOptions}
        PopperComponent={props => (
          <Popper
            {...props}
            modifiers={{
              flip: {
                enabled: false,
              },
            }}
            className={classes.popper}
          >
            {props.children}
          </Popper>
        )}
        ref={searchTextRef}
        renderInput={params => (
          <AutocompleteTextField
            params={params}
            searchTextErrorText={searchTextErrorText}
            searchTextIsError={searchTextIsError}
            submitWithoutOptionHandler={handleSubmitWithoutOption}
          />)}
        renderOption={(option, state) => (
          <AutocompleteOption
            option={option}
            link={createLink(option)}
          />
        )}
      />
    </React.Fragment>
  );
}
