import RepoCorporationInterface from "../../../repository/CorporationRepository/RepoCorporationInterface";
import React from 'react';
import Grid from '@material-ui/core/Grid';
import {createStyles, makeStyles, Theme, useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Paper from "@material-ui/core/Paper";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import { useDeps } from "../../../context/DepsContext/DepsContext";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import ClearIcon from '@material-ui/icons/Clear';
import CheckIcon from '@material-ui/icons/Check';
import EntityDescription from "../../EntityDescription/EntityDescription";
import AllianceHistoryTable from "../AllianceHistoryTable/AllianceHistoryTable";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonCell: {
      paddingTop: 0,
      paddingBottom: 0,
      width: 0,
    },
    gridContainer: {
      marginTop: 0,
    },
    gridWithTable: {
      [theme.breakpoints.down('xs')]: {
        paddingBottom: theme.spacing(3),
      },
    },
    title: {
      [theme.breakpoints.down('xs')]: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
      },
    },
    logo: {
      textAlign: "center",
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    nameCell: {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
    },
    entityLogoCell: {
      paddingTop: 0,
      paddingRight: 0,
      paddingBottom: 0
    },
    entityLogoGrid: {
      display: "flex",
      alignItems: "center",
    },
    entityLogoImg: {
      marginRight: theme.spacing(1),
      [theme.breakpoints.down('xs')]: {
        display: "none",
      },
    },
    noWrapCell: {
      whiteSpace: "nowrap",
      [theme.breakpoints.down('xs')]: {
        whiteSpace: "normal",
      },
    },
    bioCell: {
      verticalAlign: "top",
    },
    successColorText: {
      color: theme.palette.success.main,
    },
    errorColorText: {
      color: theme.palette.error.main,
    },
  }),
);

export default function CorporationInfoContent(
  {
    content
  }: {
    content: RepoCorporationInterface
  }
) {
  let classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter
  }: {
    dateFormatter: DateFormatterInterface
  } = useDeps();

  return (
    <React.Fragment>
      <Grid container spacing={isSmall ? 0 : 3} className={classes.gridContainer}>
        <Grid item xs={12} className={classes.logo}>
          <img
            alt="corporation logo"
            src={`https://images.evetech.net/corporations/${content.id}/logo`}
            height="180"
            width="180"
          />
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <TableContainer component={Paper}>
            <Table aria-label="corporation information">
              <TableBody>

                <TableRow>
                  <TableCell
                    colSpan={3}
                    align="center"
                    className={classes.nameCell}
                  >
                    <Typography variant="h6">{ content.name }</Typography>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Ticker:</TableCell>
                  <TableCell>{ content.ticker }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                {content.alliance_id !== undefined &&
                  <TableRow>
                    <TableCell>Alliance:</TableCell>
                    <TableCell className={classes.entityLogoCell}>
                      <Grid container>
                        <Grid item className={classes.entityLogoGrid}>
                          <img
                              src={`https://images.evetech.net/alliances/${content.alliance_id}/logo?size=64`}
                              alt="alliance logo"
                              height={48}
                              width={48}
                              className={classes.entityLogoImg}
                          />
                        </Grid>
                        <Grid item className={classes.entityLogoGrid}>
                          { content.alliance_name }
                        </Grid>
                      </Grid>
                    </TableCell>
                    <TableCell align="right" className={classes.buttonCell}>
                      <IconButton
                        size="small"
                        aria-label="open alliance"
                        component={Link}
                        to={`/alliance/${content.alliance_id}`}
                      >
                        <ArrowRightIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>CEO:</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    <Grid container>
                      <Grid item className={classes.entityLogoGrid}>
                        <img
                          src={`https://images.evetech.net/characters/${content.ceo_id}/portrait?size=64`}
                          alt="character portrait"
                          height={48}
                          width={48}
                          className={classes.entityLogoImg}
                        />
                      </Grid>
                      <Grid item className={classes.entityLogoGrid}>
                        { content.ceo_name }
                      </Grid>
                    </Grid>
                  </TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open character"
                      component={Link}
                      to={`/character/${content.ceo_id}`}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                {content.creator_id !== 1 && // 1 when it's created manually by CCP
                  <TableRow>
                    <TableCell>Founder:</TableCell>
                    <TableCell className={classes.entityLogoCell}>
                      <Grid container>
                        <Grid item className={classes.entityLogoGrid}>
                          <img
                            src={`https://images.evetech.net/characters/${content.creator_id}/portrait?size=64`}
                            alt="character portrait"
                            height={48}
                            width={48}
                            className={classes.entityLogoImg}
                          />
                        </Grid>
                        <Grid item className={classes.entityLogoGrid}>
                          { content.creator_name }
                        </Grid>
                      </Grid>
                    </TableCell>
                    <TableCell align="right" className={classes.buttonCell}>
                      <IconButton
                        size="small"
                        aria-label="open character"
                        component={Link}
                        to={`/character/${content.creator_id}`}
                      >
                        <ArrowRightIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Members:</TableCell>
                  <TableCell>{ content.member_count }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                {content.date_founded !== undefined &&
                  <TableRow>
                    <TableCell>Creation date:</TableCell>
                    <TableCell>{ dateFormatter.withDateTime(new Date(content.date_founded)) }</TableCell>
                    <TableCell align="right"> </TableCell>
                  </TableRow>
                }

                {content.faction_id !== undefined &&
                  <TableRow>
                    <TableCell>Faction:</TableCell>
                    <TableCell>{ content.faction_name }</TableCell>
                    <TableCell align="right"> {/* TODO: implement link when the page is implemented */}</TableCell>
                  </TableRow>
                }

                {content.home_station_id !== undefined &&
                  <TableRow>
                    <TableCell className={classes.noWrapCell}>Home station:</TableCell>
                    <TableCell>{ content.home_station_name }</TableCell>
                    <TableCell align="right" className={classes.buttonCell}>
                      {
                        /* TODO: remove when the station page is implemented
                        <IconButton
                          size="small"
                          aria-label="open station"
                          component={Link}
                          to={`/station/${content.home_station_id}`}
                        >
                          <ArrowRightIcon/>
                        </IconButton>
                        */
                      }
                    </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Tax rate:</TableCell>
                  <TableCell>{ content.tax_rate }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                {content.shares !== undefined &&
                  <TableRow>
                    <TableCell>Shares:</TableCell>
                    <TableCell>{ content.shares }</TableCell>
                    <TableCell align="right"> </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>War eligible:</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    {content.war_eligible === true
                      ? <CheckIcon className={classes.successColorText} />
                      : <ClearIcon className={classes.errorColorText} />
                    }
                  </TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>ID:</TableCell>
                  <TableCell>{ content.id }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                {content.url !== undefined &&
                  <TableRow>
                    <TableCell>Url:</TableCell>
                    <TableCell><a href={ content.url}>{ content.url }</a></TableCell>
                    <TableCell align="right"> </TableCell>
                  </TableRow>
                }

                {content.description !== undefined &&
                  <TableRow>
                    <TableCell className={classes.bioCell}>Description:</TableCell>
                    <TableCell colSpan={2}><EntityDescription bio={content.description} /></TableCell>
                  </TableRow>
                }
                </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <AllianceHistoryTable alliances={content.alliance_history} />
        </Grid>
      </Grid>
    </React.Fragment>
  )
}
