import CorporationInfoCallInterface from "./CorporationInfoCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiCorporationInfoInterface from "./EsiCorporationInfoInterface";
import EsiError from "../../../error/EsiError/EsiError";

class CorporationInfoCall implements CorporationInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(id: number): Promise<EsiCorporationInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v5/corporations/${id}/`;
  }
}

export default CorporationInfoCall;
