import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiPublicSearchInterface from "./EsiPublicSearchInterface";
import EsiError from "../../../error/EsiError/EsiError";
import QueryFactoryInterface from "../../QueryFactory/QueryFactoryInterface";
import PublicSearchCallInterface from "./PublicSearchCallInterface";
import EsiCategory from "./EsiCategory";

class PublicSearchCall implements PublicSearchCallInterface {
  private readonly _esiClient;
  private readonly _queryFactory;
  private readonly _url = "/v2/search/";

  constructor(
    esiClient: EsiClientInterface,
    queryFactory: QueryFactoryInterface
  ) {
    this._esiClient = esiClient;
    this._queryFactory = queryFactory;
  }

  /**
   * @throws Error
   */
  async get(
    searchString: string,
    categories: EsiCategory[],
    strict?: boolean
  ): Promise<EsiPublicSearchInterface> {
    let query = this._queryFactory.make();
    query.add("search", [searchString]);
    query.add("categories", categories);
    if (strict !== undefined) {
      query.add("strict", [String(strict)]);
    }

    let response = await this._esiClient.getResponse(this._url, "GET", query);
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }
}

export default PublicSearchCall;
