import StatusRepositoryInterface from "./StatusRepositoryInterface";
import StatusCallInterface from "../../service/Call/StatusCall/StatusCallInterface";

class StatusRepository implements StatusRepositoryInterface {
  private readonly _statusCall;

  constructor(statusCall: StatusCallInterface) {
    this._statusCall = statusCall;
  }

  /**
   * @throws Error
   */
  public async isEveOnline(): Promise<boolean> {
    let status = await this._statusCall.get();

    return status.players > 100;
  }
}

export default StatusRepository;
