import EsiErrorManagerInterface from "./EsiErrorManagerInterface";

class EsiErrorManager implements EsiErrorManagerInterface {
  private _errorReset = Date.now() + 60000;
  private _errorRemain = 100;

  public isErrorWindowBroken(): boolean {
    if (this._errorReset < Date.now()) {
      this._resetErrorWindow();
    }

    return this._errorRemain <= 1;
  }

  public incrementError(): void {
    this._errorRemain--;
  }

  public saveErrorWindowInfo(headers: Headers): void {
    let limitReset = headers.get("x-esi-error-limit-reset");
    let limitRemain = headers.get("x-esi-error-limit-remain");
    if (limitReset === null || limitRemain === null) {
      return;
    }

    let headerErrorReset = Number(limitReset);
    let headerErrorRemain = Number(limitRemain);

    this._errorReset = Date.now() + (headerErrorReset * 1000);
    if (headerErrorRemain < this._errorRemain) {
      this._errorRemain = headerErrorRemain;
    }
  }

  public flagWindowAsBroken(): void {
    this._errorRemain = 0;
  }

  private _resetErrorWindow(): void {
    this._errorReset = Date.now() + 60000;
    this._errorRemain = 100;
  }
}

export default EsiErrorManager;
