import SnackbarNotificationInterface from "./SnackbarNotificationInterface";

export const ENQUEUE_SNACKBAR = 'ENQUEUE_SNACKBAR';
export const CLOSE_ONE = 'CLOSE_ONE';
export const CLOSE_ALL = 'CLOSE_ALL';
export const REMOVE_SNACKBAR = 'REMOVE_SNACKBAR';

export const enqueueSnackbar = (notification: SnackbarNotificationInterface) => {
  const key = notification.options && notification.options.key;

  return {
    type: ENQUEUE_SNACKBAR,
    notification: {
      ...notification,
      key: key || new Date().getTime() + Math.random(),
    },
  };
};

export const closeOne = (key: number) => ({
  type: CLOSE_ONE,
  key,
});

export const closeAll = () => ({
  type: CLOSE_ALL,
});

export const removeOne = (key: number) => ({
  type: REMOVE_SNACKBAR,
  key,
});
