import React, {useState, useEffect} from 'react';
import { useParams } from "react-router-dom";
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Container from "@material-ui/core/Container";

import { useDeps } from "../../context/DepsContext/DepsContext";
import AppBar from "../AppBar/AppBar";
import LoadingFullPage from "../LoadingFullPage/LoadingFullPage";
import AllianceRepositoryInterface from "../../repository/AllianceRepository/AllianceRepositoryInterface";
import ApiErrorFullPage from "../ApiErrorFullPage/ApiErrorFullPage";
import AllianceInfoContent from "./AllianceInfoContent/AllianceInfoContent";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      //paddingTop: theme.spacing(3),
      paddingBottom: 0,
      paddingLeft: 0,
      paddingRight: 0,
    },
    content: {
      flexGrow: 1,
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        padding: 0,
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function AllianceInfo() {
  let classes = useStyles();
  let [apiResult, setApiResult] = useState(<LoadingFullPage />);
  let [retry, setRetry] = useState(false);
  let {
    allianceRepository
  }: {
    allianceRepository: AllianceRepositoryInterface
  } = useDeps();
  let { id } = useParams<Record<string, string | undefined>>();
  if (id === undefined) {
    // TODO: redirect to not found
  }

  useEffect(() => {
    if (id === undefined) {
      return;
    }

    setApiResult(<LoadingFullPage />);
    setRetry(false);

    allianceRepository.getOne(Number(id))
      .then(data => {
        setApiResult(
          <AllianceInfoContent content={data} />
        );
      }).catch(e => {
        setApiResult(
          <ApiErrorFullPage
            retry={() => setRetry(true)}
            text={e.message}
          />
        );
      });
  }, [retry, id]);

  return (
    <React.Fragment>
      <AppBar title="Alliance" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Container maxWidth="lg" className={classes.container}>
          {apiResult}
        </Container>
      </main>
    </React.Fragment>
  );
}
