import SettingsRepositoryInterface from "./SettingsRepositoryInterface";
import SettingsTableInterface from "../../service/EntityManager/Table/SettingsTableInterface";
import SettingsStoreInterface, { KEY_DARK_MODE } from "../../service/EntityManager/Store/SettingsStoreInterface";

class SettingsRepository implements SettingsRepositoryInterface {
  private _settingsTable;

  constructor(settingsTable: SettingsTableInterface) {
    this._settingsTable = settingsTable;
  }

  public async getDarkMode(): Promise<boolean> {
    let item = await this._settingsTable.getOne<SettingsStoreInterface>(KEY_DARK_MODE);
    if (item === undefined) {
      return false;
    }

    return item.value;
  }

  public async setDarkMode(value: boolean): Promise<void> {
    await this._settingsTable.putOne<SettingsStoreInterface>({ name: KEY_DARK_MODE, value: value });
  }
}

export default SettingsRepository;
