import React from 'react';
import { useDispatch } from 'react-redux';
import Button from '@material-ui/core/Button'

import {
  enqueueSnackbar as enqueueSnackbarAction,
  closeOne as closeOneSnackbarAction,
  closeAll as closeAllSnackbarAction
} from '../../feature/notifier/notifierActions';
import SnackbarNotificationInterface from "../../feature/notifier/SnackbarNotificationInterface";

const colorBlack = { color: "black" };

const snackbarAction = (onClickCallback: () => void) => (
  <React.Fragment>
    <Button style={ colorBlack }>
      CHECK
    </Button>
    <Button style={ colorBlack } onClick={onClickCallback}>
      DISMISS
    </Button>
  </React.Fragment>
);

export default function Test() {
  const dispatch = useDispatch();
  const enqueueSnackbar = (notification: SnackbarNotificationInterface) => dispatch(enqueueSnackbarAction(notification));
  const closeSnackbar = (key: number) => dispatch(closeOneSnackbarAction(key));
  const closeAllSnackbar = () => dispatch(closeAllSnackbarAction());

  const handleClickWarning = () => {
    // NOTE:
    // if you want to be able to dispatch a `closeSnackbar` action later on,
    // you SHOULD pass your own `key` in the options. `key` can be any sequence
    // of number or characters, but it has to be unique for a given snackbar.
    enqueueSnackbar({
      message: "Warning: API health at 56%.",
      options: {
        action: key => snackbarAction(() => closeSnackbar(Number(key))),
        anchorOrigin: {
          horizontal: "right",
          vertical: "bottom",
        },
        autoHideDuration: null,
        key: new Date().getTime() + Math.random(),
        variant: 'warning',
      },
    });
  };

  const handleClickOffline = () => {
    enqueueSnackbar({
      message: "EVE is offline, don't hit the API.",
      options: {
        action: key => snackbarAction(() => closeSnackbar(Number(key))),
        anchorOrigin: {
          horizontal: "right",
          vertical: "bottom",
        },
        autoHideDuration: null,
        key: new Date().getTime() + Math.random(),
        variant: 'error',
      },
    });
  };

  const handleDismissAll = () => {
    closeAllSnackbar();
  };

  return (
    <React.Fragment>
      <Button variant="contained" onClick={handleClickWarning}>Display low health</Button>
      <Button variant="contained" onClick={handleClickOffline}>Display offline</Button>
      <Button variant="contained" onClick={handleDismissAll}>Dismiss all snackbars</Button>
    </React.Fragment>
  );
}
