import React, { useEffect } from 'react';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import { makeStyles, useTheme, Theme, createStyles } from '@material-ui/core/styles';
import { useSelector } from 'react-redux';
import "typeface-rajdhani";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import MUISwitch from '@material-ui/core/Switch';
import { Route, Switch } from 'react-router';
import { Link } from 'react-router-dom';
import SearchIcon from '@material-ui/icons/Search';

import { useDeps } from "../../context/DepsContext/DepsContext";
import { changeDarkMode, setDarkMode } from "../../feature/theme/themeActions";
import { useAppDispatch } from "../../hooks";
import Home from "../Home/Home";
import Search from "../Search/Search";
import AllianceInfo from "../AllianceInfo/AllianceInfo";
import CharacterInfo from "../CharacterInfo/CharacterInfo";
import ConstellationInfo from "../ConstellationInfo/ConstellationInfo";
import CorporationInfo from "../CorporationInfo/CorporationInfo";
import TypeInfo from "../TypeInfo/TypeInfo";
import RegionInfo from "../RegionInfo/RegionInfo";
import SystemInfo from "../SystemInfo/SystemInfo";
import StationInfo from "../StationInfo/StationInfo";
import FactionInfo from "../FactionInfo/FactionInfo";
import RaceInfo from "../RaceInfo/RaceInfo";
import AncestryInfo from "../AncestryInfo/AncestryInfo";
import BloodlineInfo from "../BloodlineInfo/BloodlineInfo";
import Test from "../Test/Test";
import SettingsRepositoryInterface from "../../repository/SettingsRepository/SettingsRepositoryInterface";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    drawer: {
      [theme.breakpoints.up('lg')]: {
        width: drawerWidth,
        flexShrink: 0,
      },
    },
    // necessary for content to be below app bar
    toolbar: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
    drawerPaper: {
      width: drawerWidth,
    },
    logoText: {
      color: "inherit",
      fontFamily: [
        'Rajdhani',
        'sans-serif',
      ].join(','),
      fontWeight: 600,
      textDecoration: "none"
    },
    darkModeToggle: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      minHeight: 48,
    },
    nested: {
      paddingLeft: theme.spacing(4),
    },
  }),
);

interface Props {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window?: () => Window;
}

export default function ResponsiveDrawer(props: Props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  // FIXME
  // @ts-ignore
  const mobileOpen = useSelector(state => state.drawer.mobileOpen);
  // @ts-ignore
  const darkMode = useSelector(state => state.theme.darkMode);
  const dispatch = useAppDispatch() as any;
  const {
    settingsRepository
  }: {
    settingsRepository: SettingsRepositoryInterface
  } = useDeps();

  useEffect(() => {
    settingsRepository.getDarkMode()
      .then(savedDarkMode => {
        if (savedDarkMode !== darkMode) {
          dispatch(setDarkMode(savedDarkMode, false));
        }
      })
      .catch(e => console.error(e));
  }, []);

  const handleCloseDrawer = () => dispatch({type: 'drawer/mobileClose'});

  const darkModeToggle = (
    <React.Fragment>
      <Divider />
      <FormGroup row className={classes.darkModeToggle}>
        <FormControlLabel
          control={
            <MUISwitch
              checked={darkMode}
              //onChange={() => dispatch({type: 'theme/changeMode'})}
              onChange={() => dispatch(changeDarkMode)}
              name="checkedB"
              //color="primary"
              size="small"
            />
          }
          label="Dark mode"
        />
      </FormGroup>
    </React.Fragment>
  );

  const drawer = (
    <div>
      <div className={classes.toolbar}>
        <Typography
          variant="h4"
          align="center"
          className={classes.logoText}
          component={Link}
          onClick={handleCloseDrawer}
          to="/"
        >
          evelook
        </Typography>
      </div>
      <Divider />
      <List>
        <ListItem
          button
          component={Link}
          to="/search"
          onClick={handleCloseDrawer}
        >
          <ListItemIcon>
            <SearchIcon />
          </ListItemIcon>
          <ListItemText primary="Search" />
        </ListItem>
      </List>
      {
        darkModeToggle
      }
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden lgUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleCloseDrawer}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden mdDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/search">
          <Search />
        </Route>
        <Route path="/alliance/:id">
          <AllianceInfo />
        </Route>
        <Route path="/ancestry/:id">
          <AncestryInfo />
        </Route>
        <Route path="/bloodline/:id">
          <BloodlineInfo />
        </Route>
        <Route path="/character/:id">
          <CharacterInfo />
        </Route>
        <Route path="/constellation/:id">
          <ConstellationInfo />
        </Route>
        <Route path="/corporation/:id">
          <CorporationInfo />
        </Route>
        <Route path="/type/:id">
          <TypeInfo />
        </Route>
        <Route path="/race/:id">
          <RaceInfo />
        </Route>
        <Route path="/region/:id">
          <RegionInfo />
        </Route>
        <Route path="/system/:id">
          <SystemInfo />
        </Route>
        <Route path="/station/:id">
          <StationInfo />
        </Route>
        <Route path="/faction/:id">
          <FactionInfo />
        </Route>
        <Route path="/test">
          <Test />
        </Route>
      </Switch>
    </div>
  );
}
