import CorporationRepositoryInterface from "./CorporationRepositoryInterface";
import RepoCorporationInterface from "./RepoCorporationInterface";
import CorporationInfoCallInterface from "../../service/Call/CorporationInfoCall/CorporationInfoCallInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import CorporationAllianceHistoryCallInterface
  from "../../service/Call/CorporationAllianceHistoryCall/CorporationAllianceHistoryCallInterface";
import RepoCorpAllianceHistoryInterface from "./RepoCorpAllianceHistoryInterface";

class CorporationRepository implements CorporationRepositoryInterface {
  private readonly _corpInfoCall;
  private readonly _corpAllianceHistoryCall;
  private readonly _nameRepo;

  constructor(
    corpInfoCall: CorporationInfoCallInterface,
    corpAllianceHistoryCall: CorporationAllianceHistoryCallInterface,
    nameRepo: NameRepositoryInterface
  ) {
    this._corpInfoCall = corpInfoCall;
    this._corpAllianceHistoryCall = corpAllianceHistoryCall;
    this._nameRepo = nameRepo;
  }

  public async getOne(id: number): Promise<RepoCorporationInterface> {
    let allianceHistoryPromise = this.getAllianceHistory(id);

    let esiCorp = await this._corpInfoCall.get(id);
    let repoCorp: RepoCorporationInterface = {
      ...esiCorp,
      alliance_name: undefined,
      ceo_name: "#error",
      creator_name: "#error",
      faction_name: undefined,
      home_station_name: undefined,
      alliance_history: [],
      id: id,
    };

    let {
      alliance_id,
      ceo_id,
      creator_id,
      faction_id,
      home_station_id
    } = esiCorp;
    let idsToResolve: number[] = [];
    this._pushUnique(idsToResolve, ceo_id);
    this._pushUnique(idsToResolve, creator_id);
    if (alliance_id !== undefined) {
      this._pushUnique(idsToResolve, alliance_id);
    }
    if (faction_id !== undefined) {
      this._pushUnique(idsToResolve, faction_id);
    }
    if (home_station_id !== undefined) {
      this._pushUnique(idsToResolve, home_station_id);
    }

    let corpNames = await this._nameRepo.resolveGeneric(idsToResolve);
    corpNames.forEach(resolvedName => {
      let { id, name } = resolvedName;

      if (id === alliance_id) {
        repoCorp.alliance_name = name;
      }
      if (id === ceo_id) {
        repoCorp.ceo_name = name;
      }
      if (id === creator_id) {
        repoCorp.creator_name = name;
      }
      if (id === faction_id) {
        repoCorp.faction_name = name;
      }
      if (id === home_station_id) {
        repoCorp.home_station_name = name;
      }
    });

    repoCorp.alliance_history = await allianceHistoryPromise;

    return repoCorp;
  }

  public async getAllianceHistory(id: number): Promise<RepoCorpAllianceHistoryInterface[]> {
    let corpHistory = await this._corpAllianceHistoryCall.get(id);

    let idsToResolve: number[] = [];
    let recordIDs: number[] = [];
    let indexedCorpHistory: { [key: string]: RepoCorpAllianceHistoryInterface } = {};
    corpHistory.forEach(record => {
      let allianceNamePlaceholder = undefined;
      if (record.alliance_id !== undefined) {
        idsToResolve = this._pushUnique(idsToResolve, record.alliance_id);
        allianceNamePlaceholder = "#error";
      }
      recordIDs.push(record.record_id);
      indexedCorpHistory[record.record_id] = {
        ...record,
        end_date: undefined,
        alliance_name: allianceNamePlaceholder,
      };
    });
    let names = await this._nameRepo.resolveGeneric(idsToResolve);

    let historyToReturn: RepoCorpAllianceHistoryInterface[] = [];
    recordIDs.sort((a, b) => a - b);
    for (let i = 0; i < recordIDs.length; i++) {
      let id = recordIDs[i];

      // Add alliance name, if the record has the id
      if (indexedCorpHistory[id].alliance_id !== undefined) {
        for (let name of names) {
          if (name.id === indexedCorpHistory[id].alliance_id) {
            indexedCorpHistory[id].alliance_name = name.name;
            break;
          }
        }
      }

      // Add end date, if the current record is an alliance, and the following is not
      if (
        indexedCorpHistory[id].alliance_id !== undefined
        && i < recordIDs.length - 1
        && indexedCorpHistory[recordIDs[i + 1]].alliance_id === undefined
      ) {
        indexedCorpHistory[id].end_date = indexedCorpHistory[recordIDs[i + 1]].start_date;
      }

      // Add to the elements to return if it's an alliance
      if (indexedCorpHistory[id].alliance_id !== undefined) {
        historyToReturn.unshift(indexedCorpHistory[id]);
      }
    }

    return historyToReturn;
  }

  private _pushUnique(inputArray: number[], value: number): number[] {
    if (!inputArray.includes(value)) {
      inputArray.push(value);
    }

    return inputArray;
  }
}

export default CorporationRepository;
