import React, { createContext, useContext } from "react";
import Container from "../../service/Container/Container";

let DepsContext = createContext({});

export function useDeps(): any {
  return useContext(DepsContext);
}

export function DepsProvider({children, container}: {children: any, container: Container}) {
  return (
    <DepsContext.Provider value={container}>
      {children}
    </DepsContext.Provider>
  )
}
