import React from 'react';
import Grid from '@material-ui/core/Grid';
import {createStyles, makeStyles, Theme, useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Paper from "@material-ui/core/Paper";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import RepoCharacterInterface from "../../../repository/CharacterRepository/RepoCharacterInterface";
import CorpHistoryTable from "../CorpHistoryTable/CorpHistoryTable";
import { useDeps } from "../../../context/DepsContext/DepsContext";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import EntityDescription from "../../EntityDescription/EntityDescription";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonCell: {
      paddingTop: 0,
      paddingBottom: 0,
      width: 0,
    },
    gridContainer: {
      marginTop: 0,
    },
    gridWithTable: {
      [theme.breakpoints.down('xs')]: {
        paddingBottom: theme.spacing(3),
      },
    },
    title: {
      [theme.breakpoints.down('xs')]: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
      },
    },
    logo: {
      textAlign: "center",
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    nameCell: {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
    },
    entityLogoCell: {
      paddingTop: 0,
      paddingRight: 0,
      paddingBottom: 0
    },
    entityLogoGrid: {
      display: "flex",
      alignItems: "center",
    },
    entityLogoImg: {
      marginRight: theme.spacing(1),
      [theme.breakpoints.down('xs')]: {
        display: "none",
      },
    },
    bioCell: {
      verticalAlign: "top",
    },
    successColorText: {
      color: theme.palette.success.main,
    },
    errorColorText: {
      color: theme.palette.error.main,
    },
  }),
);

export default function CharacterInfoContent(
  {
    content
  }: {
    content: RepoCharacterInterface
  }
) {
  let classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter
  }: {
    dateFormatter: DateFormatterInterface
  } = useDeps();

  return (
    <React.Fragment>
      <Grid container spacing={isSmall ? 0 : 3} className={classes.gridContainer}>
        <Grid item xs={12} className={classes.logo}>
          <img
            alt="character portrait"
            src={`https://images.evetech.net/characters/${content.id}/portrait`}
            height="180"
            width="180"
          />
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <TableContainer component={Paper}>
            <Table aria-label="character information">
              <TableBody>
                <TableRow>
                  <TableCell
                    colSpan={3}
                    align="center"
                    className={classes.nameCell}
                  >
                    <Typography variant="h6">{ content.name }</Typography>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Corporation:</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    <Grid container>
                      <Grid item className={classes.entityLogoGrid}>
                        <img
                          src={`https://images.evetech.net/corporations/${content.corporation_id}/logo?size=64`}
                          alt="corporation logo"
                          height={48}
                          width={48}
                          className={classes.entityLogoImg}
                        />
                      </Grid>
                      <Grid item className={classes.entityLogoGrid}>
                        { content.corporation_name }
                      </Grid>
                    </Grid>
                  </TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open corporation"
                      component={Link}
                      to={`/corporation/${content.corporation_id}`}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                {content.alliance_name !== undefined &&
                  <TableRow>
                      <TableCell>Alliance:</TableCell>
                      <TableCell className={classes.entityLogoCell}>
                          <Grid container>
                              <Grid item className={classes.entityLogoGrid}>
                                  <img
                                      src={`https://images.evetech.net/alliances/${content.alliance_id}/logo?size=64`}
                                      alt="alliance logo"
                                      height={48}
                                      width={48}
                                      className={classes.entityLogoImg}
                                  />
                              </Grid>
                              <Grid item className={classes.entityLogoGrid}>
                                { content.alliance_name }
                              </Grid>
                          </Grid>
                      </TableCell>
                      <TableCell align="right" className={classes.buttonCell}>
                          <IconButton
                              size="small"
                              aria-label="open alliance"
                              component={Link}
                              to={`/alliance/${content.alliance_id}`}
                          >
                              <ArrowRightIcon />
                          </IconButton>
                      </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Birthday:</TableCell>
                  <TableCell>{ dateFormatter.withDateTime(new Date(content.birthday)) }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                {content.security_status !== undefined &&
                  <TableRow>
                      <TableCell>Sec status:</TableCell>
                      <TableCell
                          className={content.security_status < 0 ? classes.errorColorText : classes.successColorText}>
                        {content.security_status}
                      </TableCell>
                      <TableCell align="right"> </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Title</TableCell>
                  <TableCell>{ content.title }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Race:</TableCell>
                  <TableCell>{ content.race_name }</TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    {/* TODO: remove when the race page is implemented
                      <IconButton
                        size="small"
                        aria-label="open race"
                        component={Link}
                        to={`/race/${content.race_id}`}
                      >
                        <ArrowRightIcon/>
                      </IconButton>*/
                    }
                  </TableCell>
                </TableRow>

                {content.ancestry_id !== undefined && content.ancestry_id > 0 && // 0 is possible even if it's not valid
                  <TableRow>
                      <TableCell>Ancestry:</TableCell>
                      <TableCell>{ content.ancestry_name }</TableCell>
                      <TableCell align="right" className={classes.buttonCell}>
                        {/* TODO: remove when the ancestry page is implemented
                          <IconButton
                            size="small"
                            aria-label="open ancestry"
                            component={Link}
                            to={`/ancestry/${content.ancestry_id}`}
                          >
                            <ArrowRightIcon/>
                          </IconButton>*/
                        }
                      </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Bloodline:</TableCell>
                  <TableCell>{ content.bloodline_name }</TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    {/* TODO: remove when the bloodline page is implemented
                    <IconButton
                      size="small"
                      aria-label="open bloodline"
                      component={Link}
                      to={`/bloodline/${content.bloodline_id}`}
                    >
                      <ArrowRightIcon />
                    </IconButton>*/}
                  </TableCell>
                </TableRow>

                {content.faction_name !== undefined &&
                  <TableRow>
                      <TableCell>Faction:</TableCell>
                      <TableCell>{ content.faction_name }</TableCell>
                      <TableCell align="right" className={classes.buttonCell}>
                        {/* TODO: remove when the faction page is implemented
                          <IconButton
                              size="small"
                              aria-label="open faction"
                              component={Link}
                              to={`/faction/${content.faction_id}`}
                          >
                              <ArrowRightIcon />
                          </IconButton>*/}
                      </TableCell>
                  </TableRow>
                }
                <TableRow>
                  <TableCell>Gender:</TableCell>
                  <TableCell>{ `${content.gender[0].toUpperCase()}${content.gender.substr(1)}` }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>
                {content.description !== undefined &&
                  <TableRow>
                      <TableCell className={classes.bioCell}>Bio:</TableCell>
                      <TableCell colSpan={2}><EntityDescription bio={content.description} /></TableCell>
                  </TableRow>
                }
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <CorpHistoryTable corporations={content.corporation_history} />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
