import AllianceListCallInterface from "../../service/Call/AllianceListCall/AllianceListCallInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import AllianceRepositoryInterface from "./AllianceRepositoryInterface";
import AllianceInfoCallInterface from "../../service/Call/AllianceInfoCall/AllianceInfoCallInterface";
import RepoAllianceInterface from "./RepoAllianceInterface";
import AllianceCorporationsCallInterface
  from "../../service/Call/AllianceCorporationsCall/AllianceCorporationsCallInterface";

class AllianceRepository implements AllianceRepositoryInterface {
  private readonly _allianceListCall;
  private readonly _allianceInfoCall;
  private readonly _allianceCorpsCall;
  private readonly _nameRepo;

  constructor(
    allianceListCall: AllianceListCallInterface,
    allianceInfoCall: AllianceInfoCallInterface,
    allianceCorpsCall: AllianceCorporationsCallInterface,
    nameRepo: NameRepositoryInterface
  ) {
    this._allianceListCall = allianceListCall;
    this._allianceInfoCall = allianceInfoCall;
    this._allianceCorpsCall = allianceCorpsCall;
    this._nameRepo = nameRepo;
  }

  public async getOne(id: number): Promise<RepoAllianceInterface> {
    let esiAlliancePromise = this._allianceInfoCall.get(id);
    let esiAllianceCorpsPromise = this._allianceCorpsCall.get(id);

    let esiAlliance = await esiAlliancePromise;
    let repoAlliance: RepoAllianceInterface = {
      ...esiAlliance,
      id: id,
      creator_corporation_name: "#error",
      creator_name: "#error",
      executor_corporation_name: undefined,
      faction_name: undefined,
      corporations: {}
    };

    let {
      creator_corporation_id,
      creator_id,
      executor_corporation_id,
      faction_id
    } = esiAlliance;
    let idsToResolve: number[] = [];
    this._pushUnique(idsToResolve, creator_corporation_id);
    this._pushUnique(idsToResolve, creator_id);
    if (executor_corporation_id !== undefined) {
      this._pushUnique(idsToResolve, executor_corporation_id);
    }
    if (faction_id !== undefined) {
      this._pushUnique(idsToResolve, faction_id);
    }

    let esiAllianceCorps = await esiAllianceCorpsPromise;
    let corpsNamesPromise = this._nameRepo.resolveGeneric(esiAllianceCorps);
    let allianceNamesPromise = this._nameRepo.resolveGeneric(idsToResolve);

    let allianceNames = await allianceNamesPromise;
    allianceNames.forEach(resolvedName => {
      let { id, name } = resolvedName;
      if (id === creator_corporation_id) {
        repoAlliance.creator_corporation_name = name;
      }
      if (id === creator_id) {
        repoAlliance.creator_name = name;
      }
      if (id === executor_corporation_id) {
        repoAlliance.executor_corporation_name = name;
      }
      if (id === faction_id) {
        repoAlliance.faction_name = name;
      }
    });

    let corpsNames = await corpsNamesPromise;
    corpsNames.forEach(corpName => {
      repoAlliance.corporations[corpName.id] = corpName.name;
    });

    return repoAlliance;
  }

  /**
   * @throws Error
   */
  public async getList(): Promise<{
    [key: string]: string
  }> {
    let allianceIDs = await this._allianceListCall.get();
    let names = await this._nameRepo.resolveGeneric(allianceIDs);

    let resolved: { [key: string]: string } = {};
    names.forEach(({id, name}) => {
      resolved[id] = name;
    });

    let unresolved: number[] = [];
    allianceIDs.forEach(id => {
      if (!resolved.hasOwnProperty(id)) {
        unresolved.push(id);
      }
    });
    unresolved.forEach(id => {
      resolved[id] = `UNRESOLVED#${id}`;
    });

    return resolved;
  }

  private _pushUnique(inputArray: number[], value: number): number[] {
    if (!inputArray.includes(value)) {
      inputArray.push(value);
    }

    return inputArray;
  }
}

export default AllianceRepository;
