import React from 'react';
import { useLocation } from 'react-router-dom';

import RepoCategory, { repoCategories } from "../../repository/SearchRepository/RepoCategory";
import SearchResult from "./SearchResult/SearchResult";
import SearchEmpty from "./SearchEmpty/SearchEmpty";

export default function Search() {
  let query = new URLSearchParams(useLocation().search);

  let category: RepoCategory = "all";
  let queryCategory = query.get("category");
  if (queryCategory !== null && repoCategories.includes(queryCategory as RepoCategory)) {
    category = queryCategory as RepoCategory;
  }

  let queryStrict = query.get("strict");
  let queryNeedle = query.get("needle");
  if (queryStrict !== null && queryNeedle !== null) {
    return (
      <SearchResult
        category={category}
        needle={queryNeedle}
        strict={queryStrict === "true"}
      />
    );
  }

  return (
    <SearchEmpty category={category} />
  );
}
