import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import React, {ReactElement, useRef, useState} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    form: {
      width: "100%",
      marginBottom: theme.spacing(0.5),
      [theme.breakpoints.down('xs')]: {
        paddingLeft: theme.spacing(3),
        paddingRight: theme.spacing(3)
      }
    },
    filledGrid: {
      padding: theme.spacing(0.5),
    },
    emptyGrid: {
      padding: theme.spacing(0.5),
      // Removing the border when hitting the breakpoint
      [theme.breakpoints.down('xs')]: {
        padding: 0,
      }
    },
    selectGridItem: {
      order: 1,
      [theme.breakpoints.down('xs')]: {
        order: 2,
      }
    },
    textGridItem: {
      order: 2,
      [theme.breakpoints.down('xs')]: {
        order: 1,
        paddingBottom: theme.spacing(2),
      }
    },
    emptyGridItem: {
      order: 3,
    },
    checkGridItem: {
      order: 4,
    }
  }),
);

export default function SearchForm(
  {
    category,
    needle,
    strict
  }: {
    category: string,
    needle: string,
    strict: boolean
  }
) {
  let classes = useStyles();
  let history = useHistory();
  let [categoryValue, setCategoryValue] = useState(category);
  let [strictValue, setStrictValue] = useState(strict);
  let [searchTextErrorText, setSearchTextErrorText] = useState("");
  let [searchTextIsError, setSearchTextIsError] = useState(false);
  let searchTextRef = useRef<HTMLInputElement | null>(null);

  let submit = (): ReactElement | void => {
    let searchTextElement = searchTextRef.current;
    if (searchTextElement === null || searchTextElement.value.length < 3) {
      setSearchTextIsError(true);
      setSearchTextErrorText("It must be at least 3 characters long.")

      return;
    }

    let text = searchTextElement.value;
    history.replace(`/search?category=${categoryValue}&strict=${String(strictValue)}&needle=${text}`);
  }

  let handleEnterPress = (e: React.KeyboardEvent): void => {
    if (e.key === "Enter") {
      submit();
    }
  }

  return(
    <form autoComplete="off" className={classes.form}>
      <Grid container>
        <Grid item xs={12} sm={4} className={`${classes.filledGrid} ${classes.selectGridItem}`}>
          <FormControl
            variant="outlined"
            fullWidth
          >
            <InputLabel id="demo-simple-select-outlined-label">Category</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={categoryValue}
              onChange={e => setCategoryValue(e.target.value as string)}
              label="Category"
              color="secondary"
            >
              <MenuItem value={"all"}>All</MenuItem>
              <MenuItem value={"agent"}>Agent</MenuItem>
              <MenuItem value={"alliance"}>Alliance</MenuItem>
              <MenuItem value={"character"}>Character</MenuItem>
              <MenuItem value={"constellation"}>Constellation</MenuItem>
              <MenuItem value={"corporation"}>Corporation</MenuItem>
              <MenuItem value={"faction"}>Faction</MenuItem>
              <MenuItem value={"inventory_type"}>Item</MenuItem>
              <MenuItem value={"region"}>Region</MenuItem>
              <MenuItem value={"solar_system"}>System</MenuItem>
              <MenuItem value={"station"}>Station</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={8} className={`${classes.filledGrid} ${classes.textGridItem}`}>
          <TextField
            id="outlined-basic"
            error={searchTextIsError}
            helperText={searchTextErrorText}
            label="Search"
            variant="outlined"
            color="secondary"
            fullWidth
            onKeyPress={handleEnterPress}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={submit}
                    edge="end"
                  >
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              )
            }}
            inputRef={searchTextRef}
            defaultValue={needle}
          />
        </Grid>
        <Grid item xs={12} sm={4} className={`${classes.emptyGrid} ${classes.emptyGridItem}`}>
        </Grid>
        <Grid item xs={12} sm={8} className={`${classes.filledGrid} ${classes.checkGridItem}`}>
          <FormControlLabel
            control={
              <Checkbox
                checked={strictValue}
                onChange={e => setStrictValue(e.target.checked as boolean)}
                name="checkedA"
              />
            }
            label="Strict"
          />
        </Grid>
      </Grid>
    </form>
  );
}
