import ConstellationInfoCallInterface from "./ConstellationInfoCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiConstellationInfoInterface from "./EsiConstellationInfoInterface";
import EsiError from "../../../error/EsiError/EsiError";

class ConstellationInfoCall implements ConstellationInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  public async get(id: number): Promise<EsiConstellationInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v1/universe/constellations/${id}/`;
  }
}

export default ConstellationInfoCall;
