import React from "react";
import Container from "@material-ui/core/Container";
import SearchForm from "../SearchForm/SearchForm";
import Alert from "@material-ui/lab/Alert";
import ResultTable from "../ResultTable/ResultTable";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import ResultWithLinkInterface from "../../../repository/SearchRepository/ResulWithLinkInterface";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      padding: 0,
    }
  })
);

export default function ResultContainer(
  {
    category,
    needle,
    strict,
    resultData
  }: {
    category: string,
    needle: string,
    strict: boolean
    resultData: ResultWithLinkInterface[]
  }
) {
  let classes = useStyles();

  return (
    <React.Fragment>
      <Container maxWidth="sm" className={classes.container}>
        <SearchForm
          category={category}
          needle={needle}
          strict={strict}
        />
        {resultData.length >= 500 &&
        <Alert severity="warning">More than 500 records found, the API hasn't performed a full search. Try refining your parameters if you need more accuracy.</Alert>
        }
        <ResultTable data={resultData}/>
      </Container>
    </React.Fragment>
  )
}
