import EsiProxyInterface from "./EsiProxyInterface";
import EsiErrorManagerInterface from "../EsiErrorManager/EsiErrorManagerInterface";

/**
 * This class is used as a proxy for "fetch" inside the service worker, to keep
 * track globally (even if multiple tabs are open) of the error window.
 */
class EsiProxy implements EsiProxyInterface {
  private readonly _fetchClient;
  private readonly _esiErrorManager;

  constructor(
    fetchClient: (
      input: RequestInfo,
      init?: RequestInit
    ) => Promise<Response>,
    esiErrorManager: EsiErrorManagerInterface
  ) {
    this._fetchClient = fetchClient;
    this._esiErrorManager = esiErrorManager;
  }

  public async getResponse(
    request: Request
  ): Promise<Response> {
    if (this._esiErrorManager.isErrorWindowBroken()) {
      return new Response(
        null,
        {
          status: 420,
          statusText: "Error window broken, request blocked by service worker"
        }
      );
    }

    // Default response, this will be returned in case of network error.
    let response = new Response(
      null,
      {
        status: 502,
        statusText: "Network error inside the service worker"
      }
    );

    try {
      response = await this._fetchClient(request);
      this._esiErrorManager.saveErrorWindowInfo(response.headers);
      if (!response.ok) {
        if (response.status === 420) {  // We are error limited
          this._esiErrorManager.flagWindowAsBroken();
        } else {
          this._esiErrorManager.incrementError();
        }
      }
    } catch(e) {
      this._esiErrorManager.incrementError();
    }

    return response;
  }
}

export default EsiProxy;
