import React from 'react';
import {makeStyles, Theme, createStyles, useTheme} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { useDeps } from "../../context/DepsContext/DepsContext";
import AppBar from "../AppBar/AppBar";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import RSSRepositoryInterface from "../../repository/RSSRepository/RSSRepositoryInterface";
import RSSPaper from "./RSSPaper/RSSPaper";
import HomeSearch from "./HomeSearch/HomeSearch";
import Typography from "@material-ui/core/Typography";
import StatusCheck from "../StatusCheck/StatusCheck";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonLinkParent: {
      right: 0,
    },
    cardTitle: {
      paddingTop: theme.spacing(2),
      paddingLeft: theme.spacing(3)
    },
    content: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-between",

      height: "100vh",
      flexGrow: 1,
      padding: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        padding: 0
      }
    },
    footerText: {
      paddingTop: theme.spacing(2),
      [theme.breakpoints.down('xs')]: {
        paddingBottom: theme.spacing(1),
      }
    },
    searchGridItem: {
      minWidth: "40%",
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
      paddingTop: theme.spacing(6),
      paddingBottom: theme.spacing(9),
      [theme.breakpoints.down('lg')]: {
        minWidth: "40%",
      },
      [theme.breakpoints.down('md')]: {
        minWidth: "60%",
      },
      [theme.breakpoints.down('sm')]: {
        minWidth: "70%",
      },
      [theme.breakpoints.down('xs')]: {
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(0),
        minWidth: "100%",
      }
    },

    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function Home(props: {}) {
  const classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    rssRepository
  }: {
    rssRepository: RSSRepositoryInterface
  } = useDeps();

  return (
    <React.Fragment>
      <StatusCheck />
      <AppBar title="Home" />
      <main className={classes.content}>
        <div>
          <div className={classes.toolbar} />

          <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
          >

            <Grid item className={classes.searchGridItem} xs={6}>
              <HomeSearch />
            </Grid>

            <Grid item style={{ minWidth: "100%" }} xs={6}>
              <Grid container spacing={isSmall ? 0 : 3}>
                <RSSPaper
                  title={"Official news"}
                  source={() => rssRepository.getEveNewsFeed()}
                />
                <RSSPaper
                  title={"Dev blogs"}
                  source={() => rssRepository.getDevBlogsFeed()}
                />
                <RSSPaper
                  title={"Patch notes"}
                  source={() => rssRepository.getPatchNotesFeed()}
                />
              </Grid>
            </Grid>

          </Grid>
        </div>

        <Typography
          variant="caption"
          align="center"
          className={classes.footerText}
        >
          All Eve Related Materials are Property of CCP Games
        </Typography>
      </main>
    </React.Fragment>
  );
}
