import React, {useEffect, useState} from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';

import { useDeps } from "../../../context/DepsContext/DepsContext";
import AppBar from "../../AppBar/AppBar";
import SearchRepositoryInterface from "../../../repository/SearchRepository/SearchRepositoryInterface";
import RepoCategory from "../../../repository/SearchRepository/RepoCategory";
import LoadingFullPage from "../../LoadingFullPage/LoadingFullPage";
import ResultContainer from "../ResultContainer/ResultContainer";
import ApiErrorFullPage from "../../ApiErrorFullPage/ApiErrorFullPage";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
      height: "100vh",
      [theme.breakpoints.down('xs')]: {
        paddingLeft: 0,
        paddingRight: 0
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function SearchResult(
  {
    category,
    needle,
    strict
  }: {
    category: string,
    needle: string,
    strict: boolean
  }
) {
  let classes = useStyles();
  let [resultComponent, setResultComponent] = useState<any>(<LoadingFullPage />);
  let [retry, setRetry] = useState(false);
  let {
    searchRepository
  }: {
    searchRepository: SearchRepositoryInterface
  } = useDeps();

  useEffect(() => {
    setResultComponent(<LoadingFullPage />);
    setRetry(false);

    searchRepository.publicSearch(needle, category as RepoCategory, strict)  // FIX typescript
      .then(data => {
        setResultComponent(
          <ResultContainer
            category={category}
            needle={needle}
            strict={strict}
            resultData={data}
          />
        );
      }).catch(e => {
        setResultComponent(
          <ApiErrorFullPage
            retry={() => setRetry(true)}
            text={e.message}
          />
        );
      }
    );
  }, [category, needle, strict, retry]);

  return (
    <React.Fragment>
      <AppBar title="Search" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {resultComponent}
      </main>
    </React.Fragment>
  );
}
