import MigrationInterface from "./MigrationInterface";

class SettingsMigration implements MigrationInterface {
  public run(db: IDBDatabase, version: number): void {
    if (version <= 1) {
      let store = db.createObjectStore(
        "settings",
        {
          keyPath: "name",
          autoIncrement: false
        }
      );
      store.add({ name: "dark_mode", value: false });
    }
  }
}

export default SettingsMigration;
