import CharacterInfoCallInterface from "./CharacterInfoCallInterface";
import EsiCharacterInfoInterface from "./EsiCharacterInfoInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";

class CharacterInfoCall implements CharacterInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(id: number): Promise<EsiCharacterInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v4/characters/${id}/`;
  }
}

export default CharacterInfoCall;
