import UniverseNamesCallInterface from "./UniverseNamesCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";
import EsiUniverseNamesInterface from "./EsiUniverseNamesInterface";

class UniverseNamesCall implements UniverseNamesCallInterface {
  private readonly _esiClient;
  private readonly _url = "/v3/universe/names/";

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  async get(ids: number[]): Promise<EsiUniverseNamesInterface[]> {
    let response = await this._esiClient.getResponse(
      this._url,
      "POST",
      null,
      ids
    );

    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }
}

export default UniverseNamesCall;
