import MUIDataTable, {MUIDataTableColumnDef, MUIDataTableMeta, MUIDataTableOptions} from "mui-datatables";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import React from "react";
import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import { useDeps } from "../../../context/DepsContext/DepsContext";
import RepoCharacterCorpHistoryInterface
  from "../../../repository/CharacterRepository/RepoCharacterCorpHistoryInterface";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";

const options: MUIDataTableOptions = {
  download: false,
  elevation: 1,
  filter: false,
  fixedHeader: false,
  print: false,
  responsive: "standard",
  rowHover: false,
  rowsPerPage: 100,
  rowsPerPageOptions: [],
  search: false,
  selectableRows: "none",
  viewColumns: false,
};

const styles = {
  corpImg: {
    display: "flex",
  }
};

const extractCorpID = (tableMeta: MUIDataTableMeta): number => {
  return tableMeta.rowData[3];
};

export default function CorpHistoryTable(
  {
    corporations
  }: {
    corporations: RepoCharacterCorpHistoryInterface[]
  }
) {
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter
  }: {
    dateFormatter: DateFormatterInterface
  } = useDeps();

  const columns: MUIDataTableColumnDef[] = [
    {
      name: "logo",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = extractCorpID(tableMeta);

          return (
            <img
              src={`https://images.evetech.net/corporations/${id}/logo?size=64`}
              alt="corporation logo"
              height={48}
              width={48}
              style={styles.corpImg}
            />
          );
        },
        display: !isSmall,
        setCellProps: () => ({ style: { width: 0, paddingTop: 0, paddingBottom: 0, paddingRight: 0 }}),
      }
    },
    {
      name: "corporation_name",
      label: "Name",
      options: {
        customHeadRender: () => null
      }
    },
    {
      name: "start_date",
      label: "From",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          return dateFormatter.withDateTime(new Date(value));
        },
      }
    },
    {
      name: "corporation_id",
      label: "ID",
      options: {
        customHeadRender: () => null,
        display: false,
      }
    },
    {
      name: "icon",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = extractCorpID(tableMeta);

          return (
            <IconButton
              size="small"
              aria-label="open"
              component={Link}
              to={`/corporation/${id}`}
            >
              <ArrowRightIcon />
            </IconButton>
          );
        },
        download: false,
        setCellProps: () => ({ style: { width: 0, paddingTop: 0, paddingBottom: 0 }}),
      }
    }
  ];

  return(
    <MUIDataTable
      title={"Corp History"}
      data={corporations}
      columns={columns}
      options={options}
    />
  );
}
