import DateFormatterInterface from "./DateFormatterInterface";

class DateFormatter implements DateFormatterInterface {
  public withDateTime(date: Date): string {
    let yyyy = `${date.getUTCFullYear()}`;
    let mm = this.toStringWithLeadingZero(date.getUTCMonth() + 1);
    let dd = this.toStringWithLeadingZero(date.getUTCDate());
    let hr = this.toStringWithLeadingZero(date.getUTCHours());
    let min = this.toStringWithLeadingZero(date.getUTCMinutes());
    let sec = this.toStringWithLeadingZero(date.getUTCSeconds());

    return `${yyyy}.${mm}.${dd} ${hr}:${min}:${sec}`;
  }

  public onlyDate(date: Date): string {
    let yyyy = `${date.getUTCFullYear()}`;
    let mm = this.toStringWithLeadingZero(date.getUTCMonth() + 1);
    let dd = this.toStringWithLeadingZero(date.getUTCDate());

    return `${yyyy}.${mm}.${dd}`;
  }

  private toStringWithLeadingZero(value: number): string {
    if (value < 10) {
      return `0${value}`;
    }

    return `${value}`;
  }
}

export default DateFormatter;
