import React, { useEffect } from "react";
import { useDispatch } from 'react-redux';

import { useDeps } from "../../context/DepsContext/DepsContext";
import StatusRepositoryInterface from "../../repository/StatusRepository/StatusRepositoryInterface";
import {
  enqueueSnackbar as enqueueSnackbarAction,
  closeOne as closeOneSnackbarAction
} from '../../feature/notifier/notifierActions';import Button from "@material-ui/core/Button";
import { KEY_EVE_OFFLINE } from "../../feature/notifier/notifierKeys";
import SnackbarNotificationInterface from "../../feature/notifier/SnackbarNotificationInterface";

const colorBlack = { color: "black" };

const snackbarAction = (onClickCallback: () => void) => (
  <React.Fragment>
    <Button style={ colorBlack }>
      CHECK
    </Button>
    <Button style={ colorBlack } onClick={onClickCallback}>
      DISMISS
    </Button>
  </React.Fragment>
);

export default function StatusCheck() {
  const dispatch = useDispatch();
  const enqueueSnackbar = (notification: SnackbarNotificationInterface) => dispatch(enqueueSnackbarAction(notification));
  const closeSnackbar = (key: number) => dispatch(closeOneSnackbarAction(key));
  const {
    statusRepository
  }: {
    statusRepository: StatusRepositoryInterface
  } = useDeps();

  useEffect(() => {
    statusRepository.isEveOnline()
      .then(isOnline => {
        if (!isOnline) {
          enqueueSnackbar({
            message: "EVE is offline, don't hit the API.",
            options: {
              action: key => snackbarAction(() => closeSnackbar(Number(key))),
              anchorOrigin: {
                horizontal: "right",
                vertical: "bottom",
              },
              autoHideDuration: null,
              key: KEY_EVE_OFFLINE,
              preventDuplicate: true,
              variant: 'error',
            },
          });
        }
      })
      .catch(e => console.log(`Error while checking EVE status: ${e}`));
  });

  return null;
}
