import { THEME_CHANGE_MODE, THEME_SET_MODE } from "./themeActions";

const initialState = {
  darkMode: false,
}

export default function themeReducer(
  state = initialState,
  action: {type: string, payload?: any}
) {
  switch (action.type) {
    case THEME_CHANGE_MODE: {
      return {
        ...state,
        darkMode: !state.darkMode,
      };
    }
    case THEME_SET_MODE: {
      return {
        ...state,
        darkMode: action.payload,
      };
    }
    default:
      return state;
  }
}