import BloodlineRepositoryInterface from "./BloodlineRepositoryInterface";
import BloodlineCallInterface from "../../service/Call/BloodlineCall/BloodlineCallInterface";
import EsiBloodlineInterface from "../../service/Call/BloodlineCall/EsiBloodlineInterface";
import RepoBloodlineInterface from "./RepoBloodlineInterface";
import RaceRepositoryInterface from "../RaceRepository/RaceRepositoryInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import AbstractRepository from "../AbstractRepository/AbstractRepository";
import EsiUniverseNamesInterface from "../../service/Call/UniverseNamesCall/EsiUniverseNamesInterface";

class BloodlineRepository extends AbstractRepository implements BloodlineRepositoryInterface {
  private readonly _bloodlineCall;
  private readonly _raceRepo;
  private readonly _nameRepo;

  constructor(
    bloodlineCall: BloodlineCallInterface,
    raceRepo: RaceRepositoryInterface,
    nameRepo: NameRepositoryInterface
  ) {
    super();
    this._bloodlineCall = bloodlineCall;
    this._raceRepo = raceRepo;
    this._nameRepo = nameRepo;
  }

  /**
   * @throws Error
   */
  public async getAll(): Promise<RepoBloodlineInterface[]> {
    return [];
  }

  /**
   * @throws Error
   */
  public async getOne(id: number): Promise<RepoBloodlineInterface> {
    let foundBloodline = await this._getEsiBloodline(id);
    let {
      race_id,
      corporation_id,
      ship_type_id
    } = foundBloodline;

    let raceNamePromise = this._raceRepo.getName(race_id);

    let idsToResolve = this._getIDsToResolve([foundBloodline]);
    let indexedNames: {[key: string]: EsiUniverseNamesInterface} = {};
    if (idsToResolve.length > 0) {
      indexedNames = this._orderNamesByID(await this._nameRepo.resolveGeneric(idsToResolve));
    }

    let ship_type_name = undefined;
    if (ship_type_id !== undefined && ship_type_id !== null) {
      ship_type_name = indexedNames[ship_type_id].name ?? "#error";
    }

    return {
      ...foundBloodline,
      corporation_name: indexedNames[corporation_id].name ?? "#error",
      race_name: await raceNamePromise,
      ship_type_name: ship_type_name
    }
  }

  /**
   * @throws Error
   */
  public async getName(id: number): Promise<string> {
    let foundBloodline = await this._getEsiBloodline(id);

    return foundBloodline.name;
  }

  private _getIDsToResolve(bloodlines: EsiBloodlineInterface[]): number[] {
    let idsToResolve: number[] = [];

    bloodlines.forEach(bloodline => {
      let {
        corporation_id,
        ship_type_id
      } = bloodline;
      this._pushUnique(idsToResolve, corporation_id);
      if (ship_type_id !== undefined && ship_type_id !== null) {
        this._pushUnique(idsToResolve, ship_type_id);
      }
    });

    return idsToResolve;
  }

  /**
   * @throws Error
   */
  private async _getEsiBloodline(id: number): Promise<EsiBloodlineInterface> {
    let bloodlines = await this._bloodlineCall.get();
    let foundBloodline: EsiBloodlineInterface | null = null;
    for (let bloodline of bloodlines) {
      if (bloodline.bloodline_id === id) {
        foundBloodline = bloodline;
        break;
      }
    }

    if (foundBloodline === null) {
      throw new Error("Bloodline not found");
    }

    return foundBloodline;
  }
}

export default BloodlineRepository;
