import RaceRepositoryInterface from "./RaceRepositoryInterface";
import RaceCallInterface from "../../service/Call/RaceCall/RaceCallInterface";
import EsiRaceInterface from "../../service/Call/RaceCall/EsiRaceInterface";
import RepoRaceInterface from "./RepoRaceInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import AbstractRepository from "../AbstractRepository/AbstractRepository";
import FactionRepositoryInterface from "../FactionRepository/FactionRepositoryInterface";

class RaceRepository extends AbstractRepository implements RaceRepositoryInterface {
  private readonly _raceCall;
  private readonly _nameRepo;
  private readonly _factionRepo;

  constructor(
    raceCall: RaceCallInterface,
    nameRepo: NameRepositoryInterface,
    factionRepo: FactionRepositoryInterface
  ) {
    super();
    this._raceCall = raceCall;
    this._nameRepo = nameRepo;
    this._factionRepo = factionRepo;
  }

  /**
   * @throws Error
   */
  public async getAll(): Promise<RepoRaceInterface[]> {
    let esiRaces = await this._raceCall.get();
    let idsToResolve: number[] = [];
    let repoRaces: RepoRaceInterface[] = [];
    esiRaces.forEach(esiRace => {
      this._pushUnique(idsToResolve, esiRace.alliance_id);
      repoRaces.push({
        ...esiRace,
        alliance_name: "#error",
      });
    });

    let names = this._orderNamesByID(await this._nameRepo.resolveGeneric(idsToResolve));
    repoRaces.forEach(repoRace => {
      if (names[String(repoRace.alliance_id)] !== undefined) {
        repoRace.alliance_name = names[String(repoRace.alliance_id)].name;
      }
    });

    return repoRaces;
  }

  /**
   * @throws Error
   */
  public async getOne(id: number): Promise<RepoRaceInterface> {
    let foundRace = await this._getEsiRace(id);

    return {
      ...foundRace,
      alliance_name: await this._factionRepo.getName(foundRace.alliance_id)
    };
  }

  /**
   * @throws Error
   */
  public async getName(id: number): Promise<string> {
    let foundRace = await this._getEsiRace(id);

    return foundRace.name;
  }

  /**
   * @throws Error
   */
  private async _getEsiRace(id: number): Promise<EsiRaceInterface> {
    let races = await this._raceCall.get();
    let foundRace: EsiRaceInterface | null = null;
    for (let race of races) {
      if (race.race_id === id) {
        foundRace = race;
        break;
      }
    }

    if (foundRace === null) {
      throw new Error("Race not found");
    }

    return foundRace;
  }
}

export default RaceRepository;
