import AllianceListCallInterface from "./AllianceListCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";

class AllianceListCall implements AllianceListCallInterface {
  private readonly _esiClient;
  private readonly _url = "/v2/alliances/";

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  async get(): Promise<number[]> {
    let response = await this._esiClient.getResponse(this._url, "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }
}

export default AllianceListCall;
