import MUIDataTable, {MUIDataTableColumnDef, MUIDataTableMeta, MUIDataTableOptions} from "mui-datatables";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import React from "react";
import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Grid from '@material-ui/core/Grid';
import { useDeps } from "../../../context/DepsContext/DepsContext";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";
import RepoCorpAllianceHistoryInterface
  from "../../../repository/CorporationRepository/RepoCorpAllianceHistoryInterface";


const options: MUIDataTableOptions = {
  download: false,
  elevation: 1,
  filter: false,
  fixedHeader: false,
  print: false,
  responsive: "standard",
  rowHover: false,
  rowsPerPage: 100,
  rowsPerPageOptions: [],
  search: false,
  selectableRows: "none",
  viewColumns: false,
};

const styles = {
  allianceImg: {
    display: "flex",
  }
};

const extractAllianceID = (tableMeta: MUIDataTableMeta): number => {
  return tableMeta.rowData[5];
};

export default function AllianceHistoryTable(
  {
    alliances
  }: {
    alliances: RepoCorpAllianceHistoryInterface[]
  }
) {
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter
  }: {
    dateFormatter: DateFormatterInterface
  } = useDeps();

  const columns: MUIDataTableColumnDef[] = [
    {
      name: "logo",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = extractAllianceID(tableMeta);

          return (
            <img
              src={`https://images.evetech.net/alliances/${id}/logo?size=64`}
              alt="alliance logo"
              height={48}
              width={48}
              style={styles.allianceImg}
            />
          );
        },
        display: !isSmall,
        setCellProps: () => ({ style: { width: 0, paddingTop: 0, paddingBottom: 0, paddingRight: 0 }}),
      }
    },
    {
      name: "alliance_name",
      label: "Name",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let isDeleted = tableMeta.rowData[7];

          return `${value}${isDeleted === true ? " (Closed)" : ""}`;
        },
      }
    },
    {
      name: "start_date",
      label: "From",
      options: {
        display: false,
      }
    },
    {
      name: "end_date",
      label: "To",
      options: {
        display: false,
      }
    },
    {
      name: "stacked_dates",
      label: "From To",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let start = tableMeta.rowData[2];
          let end = tableMeta.rowData[3];

          return (
            <React.Fragment>
              <Grid container direction="column">
                {end !== undefined &&
                  <Grid item xs style={{ whiteSpace: "nowrap" }}>
                    {dateFormatter.withDateTime(new Date(end))}
                  </Grid>
                }
                <Grid item xs style={{ whiteSpace: "nowrap" }}>
                  {dateFormatter.withDateTime(new Date(start))}
                </Grid>
              </Grid>
            </React.Fragment>
          );
        },
        setCellProps: () => ({ style: { paddingTop: 0, paddingBottom: 0 }}),
      }
    },
    {
      name: "alliance_id",
      label: "ID",
      options: {
        customHeadRender: () => null,
        display: false,
      }
    },
    {
      name: "icon",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = extractAllianceID(tableMeta);

          return (
            <IconButton
              size="small"
              aria-label="open"
              component={Link}
              to={`/alliance/${id}`}
            >
              <ArrowRightIcon/>
            </IconButton>
          );
        },
        download: false,
        setCellProps: () => ({style: {width: 0, paddingTop: 0, paddingBottom: 0}}),
      }
    },
    {
      name: "is_deleted",
      label: "Is deleted",
      options: {
        display: false,
      }
    },
  ];

  return(
    <MUIDataTable
      title={"Alliance History"}
      data={alliances}
      columns={columns}
      options={options}
    />
  );
}
