import UniverseNamesCallInterface from "../Call/UniverseNamesCall/UniverseNamesCallInterface";
import UniverseNamesCall from "../Call/UniverseNamesCall/UniverseNamesCall";
import AllianceListCallInterface from "../Call/AllianceListCall/AllianceListCallInterface";
import AllianceListCall from "../Call/AllianceListCall/AllianceListCall";
import AllianceInfoCallInterface from "../Call/AllianceInfoCall/AllianceInfoCallInterface";
import AllianceInfoCall from "../Call/AllianceInfoCall/AllianceInfoCall";
import NameRepositoryInterface from "../../repository/NameRepository/NameRepositoryInterface";
import NameRepository from "../../repository/NameRepository/NameRepository";
import AllianceCorporationsCallInterface from "../Call/AllianceCorporationsCall/AllianceCorporationsCallInterface";
import AllianceCorporationsCall from "../Call/AllianceCorporationsCall/AllianceCorporationsCall";
import PublicSearchCallInterface from "../Call/PublicSearchCall/PublicSearchCallInterface";
import PublicSearchCall from "../Call/PublicSearchCall/PublicSearchCall";
import AllianceRepositoryInterface from "../../repository/AllianceRepository/AllianceRepositoryInterface";
import AllianceRepository from "../../repository/AllianceRepository/AllianceRepository";
import SearchRepositoryInterface from "../../repository/SearchRepository/SearchRepositoryInterface";
import SearchRepository from "../../repository/SearchRepository/SearchRepository";
import CharacterInfoCallInterface from "../Call/CharacterInfoCall/CharacterInfoCallInterface";
import CharacterInfoCall from "../Call/CharacterInfoCall/CharacterInfoCall";
import ConstellationInfoCallInterface from "../Call/ConstellationInfoCall/ConstellationInfoCallInterface";
import ConstellationInfoCall from "../Call/ConstellationInfoCall/ConstellationInfoCall";
import CorporationInfoCall from "../Call/CorporationInfoCall/CorporationInfoCall";
import TypeInfoCallInterface from "../Call/TypeInfoCall/TypeInfoCallInterface";
import TypeInfoCall from "../Call/TypeInfoCall/TypeInfoCall";
import RegionInfoCallInterface from "../Call/RegionInfoCall/RegionInfoCallInterface";
import RegionInfoCall from "../Call/RegionInfoCall/RegionInfoCall";
import SystemInfoCallInterface from "../Call/SystemInfoCall/SystemInfoCallInterface";
import SystemInfoCall from "../Call/SystemInfoCall/SystemInfoCall";
import StationInfoCall from "../Call/StationInfoCall/StationInfoCall";
import StationInfoCallInterface from "../Call/StationInfoCall/StationInfoCallInterface";
import FactionCallInterface from "../Call/FactionCall/FactionCallInterface";
import CorporationInfoCallInterface from "../Call/CorporationInfoCall/CorporationInfoCallInterface";
import FactionCall from "../Call/FactionCall/FactionCall";
import AncestryCallInterface from "../Call/AncestryCall/AncestryCallInterface";
import AncestryCall from "../Call/AncestryCall/AncestryCall";
import BloodlineCallInterface from "../Call/BloodlineCall/BloodlineCallInterface";
import BloodlineCall from "../Call/BloodlineCall/BloodlineCall";
import AncestryRepositoryInterface from "../../repository/AncestryRepository/AncestryRepositoryInterface";
import AncestryRepository from "../../repository/AncestryRepository/AncestryRepository";
import BloodlineRepositoryInterface from "../../repository/BloodlineRepository/BloodlineRepositoryInterface";
import BloodlineRepository from "../../repository/BloodlineRepository/BloodlineRepository";
import CharacterRepository from "../../repository/CharacterRepository/CharacterRepository";
import CharacterRepositoryInterface from "../../repository/CharacterRepository/CharacterRepositoryInterface";
import RaceCallInterface from "../Call/RaceCall/RaceCallInterface";
import RaceCall from "../Call/RaceCall/RaceCall";
import RaceRepositoryInterface from "../../repository/RaceRepository/RaceRepositoryInterface";
import RaceRepository from "../../repository/RaceRepository/RaceRepository";
import CharacterCorpHistoryCallInterface from "../Call/CharacterCorpHistoryCall/CharacterCorpHistoryCallInterface";
import CharacterCorpHistoryCall from "../Call/CharacterCorpHistoryCall/CharacterCorpHistoryCall";
import DateFormatterInterface from "../DateFormatter/DateFormatterInterface";
import DateFormatter from "../DateFormatter/DateFormatter";
import CorporationAllianceHistoryCallInterface
  from "../Call/CorporationAllianceHistoryCall/CorporationAllianceHistoryCallInterface";
import CorporationAllianceHistoryCall from "../Call/CorporationAllianceHistoryCall/CorporationAllianceHistoryCall";
import CorporationRepositoryInterface from "../../repository/CorporationRepository/CorporationRepositoryInterface";
import CorporationRepository from "../../repository/CorporationRepository/CorporationRepository";
import FactionRepositoryInterface from "../../repository/FactionRepository/FactionRepositoryInterface";
import FactionRepository from "../../repository/FactionRepository/FactionRepository";
import RSSRepositoryInterface from "../../repository/RSSRepository/RSSRepositoryInterface";
import RSSRepository from "../../repository/RSSRepository/RSSRepository";
import StatusRepositoryInterface from "../../repository/StatusRepository/StatusRepositoryInterface";
import StatusRepository from "../../repository/StatusRepository/StatusRepository";
import EntityManagerInterface from "../EntityManager/EntityManagerInterface";
import EntityManager from "../EntityManager/EntityManager";
import MarketOrdersMigration from "../EntityManager/Migration/MarketOrdersMigration";
import SettingsMigration from "../EntityManager/Migration/SettingsMigration";
import SettingsTable from "../EntityManager/Table/SettingsTable";
import SettingsTableInterface from "../EntityManager/Table/SettingsTableInterface";
import SettingsRepositoryInterface from "../../repository/SettingsRepository/SettingsRepositoryInterface";
import SettingsRepository from "../../repository/SettingsRepository/SettingsRepository";
import EsiProxyInterface from "../EsiProxy/EsiProxyInterface";
import EsiProxy from "../EsiProxy/EsiProxy";

class Container {
  private _esi: EsiClient | null = null;
  private _esiProxy: EsiProxyInterface | null = null;
  private _esiErrorManager: EsiErrorManagerInterface | null = null;

  get dateFormatter(): DateFormatterInterface {
    return new DateFormatter();
  }

  get entityManager(): EntityManagerInterface {
    return new EntityManager(
      [
        new MarketOrdersMigration(),
        new SettingsMigration()
      ]
    );
  }

  get esi(): EsiClient {
    if (this._esi === null) {
      this._esi = new EsiClient(
        (input: RequestInfo, init?: RequestInit): Promise<Response> => {
          return fetch(input, init);
        },
        this.esiErrorManager
      );
    }

    return this._esi;
  }

  get esiErrorManager(): EsiErrorManagerInterface {
    if (this._esiErrorManager === null) {
      this._esiErrorManager = new EsiErrorManager();
    }

    return this._esiErrorManager;
  }

  get esiProxy(): EsiProxyInterface {
    if (this._esiProxy === null) {
      this._esiProxy = new EsiProxy(
        (input: RequestInfo, init?: RequestInit): Promise<Response> => {
          return fetch(input, init);
        },
        this.esiErrorManager
      );
    }

    return this._esiProxy;
  }

  get queryFactory(): QueryFactoryInterface {
    return new QueryFactory();
  }

  // CALL //-------------------------------------------------------------------

  get allianceCorporationsCall(): AllianceCorporationsCallInterface {
    return new AllianceCorporationsCall(this.esi);
  }

  get allianceInfoCall(): AllianceInfoCallInterface {
    return new AllianceInfoCall(this.esi);
  }

  get allianceListCall(): AllianceListCallInterface {
    return new AllianceListCall(this.esi);
  }

  get ancestryCall(): AncestryCallInterface {
    return new AncestryCall(this.esi);
  }

  get bloodlineCall(): BloodlineCallInterface {
    return new BloodlineCall(this.esi);
  }

  get characterCorpHistoryCall(): CharacterCorpHistoryCallInterface {
    return new CharacterCorpHistoryCall(this.esi);
  }

  get characterInfoCall(): CharacterInfoCallInterface {
    return new CharacterInfoCall(this.esi);
  }

  get constellationInfoCall(): ConstellationInfoCallInterface {
    return new ConstellationInfoCall(this.esi);
  }

  get corporationAllianceHistoryCall(): CorporationAllianceHistoryCallInterface {
    return new CorporationAllianceHistoryCall(this.esi);
  }

  get corporationInfoCall(): CorporationInfoCallInterface {
    return new CorporationInfoCall(this.esi);
  }

  get factionCall(): FactionCallInterface {
    return new FactionCall(this.esi);
  }

  get publicSearchCall(): PublicSearchCallInterface {
    return new PublicSearchCall(this.esi, this.queryFactory);
  }

  get raceCall(): RaceCallInterface {
    return new RaceCall(this.esi);
  }

  get regionInfoCall(): RegionInfoCallInterface {
    return new RegionInfoCall(this.esi);
  }

  get statusCall(): StatusCallInterface {
    return new StatusCall(this.esi);
  }

  get stationInfoCall(): StationInfoCallInterface {
    return new StationInfoCall(this.esi);
  }

  get systemInfoCall(): SystemInfoCallInterface {
    return new SystemInfoCall(this.esi);
  }

  get typeInfoCall(): TypeInfoCallInterface {
    return new TypeInfoCall(this.esi);
  }

  get universeNamesCall(): UniverseNamesCallInterface {
    return new UniverseNamesCall(this.esi);
  }

  // CALL - END // ------------------------------------------------------------

  // REPOSITORY //-------------------------------------------------------------

  get allianceRepository(): AllianceRepositoryInterface {
    return new AllianceRepository(
      this.allianceListCall,
      this.allianceInfoCall,
      this.allianceCorporationsCall,
      this.nameRepository
    );
  }

  get ancestryRepository(): AncestryRepositoryInterface {
    return new AncestryRepository(this.ancestryCall);
  }

  get bloodlineRepository(): BloodlineRepositoryInterface {
    return new BloodlineRepository(
      this.bloodlineCall,
      this.raceRepository,
      this.nameRepository
    );
  }

  get characterRepository(): CharacterRepositoryInterface {
    return new CharacterRepository(
      this.characterInfoCall,
      this.characterCorpHistoryCall,
      this.ancestryRepository,
      this.bloodlineRepository,
      this.nameRepository,
      this.raceRepository
    );
  }

  get corporationRepository(): CorporationRepositoryInterface {
    return new CorporationRepository(
      this.corporationInfoCall,
      this.corporationAllianceHistoryCall,
      this.nameRepository
    );
  }

  get factionRepository(): FactionRepositoryInterface {
    return new FactionRepository(
      this.factionCall,
      this.nameRepository
    );
  }

  get nameRepository(): NameRepositoryInterface {
    return new NameRepository(this.universeNamesCall);
  }

  get raceRepository(): RaceRepositoryInterface {
    return new RaceRepository(
      this.raceCall,
      this.nameRepository,
      this.factionRepository
    );
  }

  get rssRepository(): RSSRepositoryInterface {
    return new RSSRepository();
  }

  get searchRepository(): SearchRepositoryInterface {
    return new SearchRepository(this.nameRepository, this.publicSearchCall);
  }

  get settingsRepository(): SettingsRepositoryInterface {
    return new SettingsRepository(this.settingsTable);
  }

  get statusRepository(): StatusRepositoryInterface {
    return new StatusRepository(this.statusCall);
  }

  // REPOSITORY - END // ------------------------------------------------------

  // TABLE //------------------------------------------------------------------

  get settingsTable(): SettingsTableInterface {
    return new SettingsTable(this.entityManager);
  }
}

export default Container;
