import CharacterCorpHistoryCallInterface from "./CharacterCorpHistoryCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiCharacterCorpHistoryInterface from "./EsiCharacterCorpHistoryInterface";
import EsiError from "../../../error/EsiError/EsiError";

class CharacterCorpHistoryCall implements CharacterCorpHistoryCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  public async get(id: number): Promise<EsiCharacterCorpHistoryInterface[]> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v1/characters/${id}/corporationhistory/`;
  }
}

export default CharacterCorpHistoryCall;
