import FactionRepositoryInterface from "./FactionRepositoryInterface";
import RepoFactionInterface from "./RepoFactionInterface";
import AbstractRepository from "../AbstractRepository/AbstractRepository";
import FactionCallInterface from "../../service/Call/FactionCall/FactionCallInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import EsiUniverseNamesInterface from "../../service/Call/UniverseNamesCall/EsiUniverseNamesInterface";
import EsiFactionInterface from "../../service/Call/FactionCall/EsiFactionInterface";

class FactionRepository extends AbstractRepository implements FactionRepositoryInterface {
  private readonly _factionCall;
  private readonly _nameRepo;

  constructor(
    factionCall: FactionCallInterface,
    nameRepo: NameRepositoryInterface
  ) {
    super();
    this._factionCall = factionCall;
    this._nameRepo = nameRepo;
  }

  /**
   * @throws Error
   */
  public async getAll(): Promise<RepoFactionInterface[]> {
    let esiFactions = await this._factionCall.get();
    let idsToResolve: number[] = this._getIDsToResolve(esiFactions);
    let indexedNames: {[key: string]: EsiUniverseNamesInterface} = {}
    if (idsToResolve.length > 0) {
      indexedNames = this._orderNamesByID(await this._nameRepo.resolveGeneric(idsToResolve));
    }

    let repoFactions: RepoFactionInterface[] = [];
    esiFactions.forEach(esiFaction => {
      repoFactions.push(this._assembleRepoFaction(esiFaction, indexedNames));
    });

    return repoFactions;
  }

  /**
   * @throws Error
   */
  public async getOne(id: number): Promise<RepoFactionInterface> {
    let foundFaction = await this._getEsiFaction(id);

    let idsToResolve = this._getIDsToResolve([foundFaction]);
    let indexedNames: {[key: string]: EsiUniverseNamesInterface} = {}
    if (idsToResolve.length > 0) {
      indexedNames = this._orderNamesByID(await this._nameRepo.resolveGeneric(idsToResolve));
    }

    return this._assembleRepoFaction(foundFaction, indexedNames);
  }

  /**
   * @throws Error
   */
  public async getName(id: number): Promise<string> {
    let foundFaction = await this._getEsiFaction(id);

    return foundFaction.name;
  }

  /**
   * @throws Error
   */
  private async _getEsiFaction(id: number): Promise<EsiFactionInterface> {
    let esiFactions = await this._factionCall.get();
    let foundFaction: EsiFactionInterface | null = null;
    for (let esiFaction of esiFactions) {
      if (esiFaction.faction_id === id) {
        foundFaction = esiFaction;
        break;
      }
    }
    if (foundFaction === null) {
      throw new Error("Faction not found");
    }

    return foundFaction;
  }

  private _getIDsToResolve(factions: EsiFactionInterface[]): number[] {
    let idsToResolve: number[] = [];

    factions.forEach(faction => {
      let {
        corporation_id,
        militia_corporation_id,
        solar_system_id
      } = faction;
      if (corporation_id !== undefined) {
        this._pushUnique(idsToResolve, corporation_id);
      }
      if (militia_corporation_id !== undefined) {
        this._pushUnique(idsToResolve, militia_corporation_id);
      }
      if (solar_system_id !== undefined) {
        this._pushUnique(idsToResolve, solar_system_id);
      }
    });

    return idsToResolve;
  }

  private _assembleRepoFaction(
    esiFaction: EsiFactionInterface,
    indexedNames: {[key: string]: EsiUniverseNamesInterface}
  ): RepoFactionInterface {
    let {
      corporation_id,
      militia_corporation_id,
      solar_system_id
    } = esiFaction;

    let corporation_name = undefined;
    let militia_corporation_name = undefined;
    let solar_system_name = undefined;

    if (corporation_id !== undefined) {
      corporation_name = indexedNames[corporation_id].name ?? "#error";
    }
    if (militia_corporation_id !== undefined) {
      militia_corporation_name = indexedNames[militia_corporation_id].name ?? "#error";
    }
    if (solar_system_id !== undefined) {
      solar_system_name = indexedNames[solar_system_id].name ?? "#error";
    }

    return {
      ...esiFaction,
      corporation_name: corporation_name,
      militia_corporation_name: militia_corporation_name,
      solar_system_name: solar_system_name
    };
  }
}

export default FactionRepository;
