import React, {useEffect, useState} from 'react';
import { useParams } from "react-router-dom";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import AppBar from "../AppBar/AppBar";
import Container from "@material-ui/core/Container";
import LoadingFullPage from "../LoadingFullPage/LoadingFullPage";
import { useDeps } from "../../context/DepsContext/DepsContext";
import ApiErrorFullPage from "../ApiErrorFullPage/ApiErrorFullPage";
import CorporationRepositoryInterface from "../../repository/CorporationRepository/CorporationRepositoryInterface";
import CorporationInfoContent from "./CorporationInfoContent/CorporationInfoContent";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      paddingBottom: 0,
      paddingLeft: 0,
      paddingRight: 0,
    },
    content: {
      flexGrow: 1,
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        padding: 0,
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function CorporationInfo() {
  let classes = useStyles();
  let [apiResult, setApiResult] = useState(<LoadingFullPage />);
  let [retry, setRetry] = useState(false);
  let {
    corporationRepository
  }: {
    corporationRepository: CorporationRepositoryInterface
  } = useDeps();
  let { id } = useParams<Record<string, string | undefined>>();
  if (id === undefined) {
    // TODO: redirect to not found
  }

  useEffect(() => {
    if (id === undefined) {
      return;
    }

    setApiResult(<LoadingFullPage />);
    setRetry(false);

    corporationRepository.getOne(Number(id))
      .then(data => {
        setApiResult(
          <CorporationInfoContent content={data} />
        )
      }).catch(e => {
        setApiResult(
          <ApiErrorFullPage
            retry={() => setRetry(true)}
            text={e.message}
          />
        );
      });
  }, [retry, id]);

  return (
    <React.Fragment>
      <AppBar title="Corporation" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Container maxWidth="lg" className={classes.container}>
          {apiResult}
        </Container>
      </main>
    </React.Fragment>
  );
}
