import React from 'react';
import RSSFeedEntryInterface from "../../../repository/RSSRepository/RSSFeedEntryInterface";
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import {makeStyles, Theme, createStyles, useTheme} from '@material-ui/core/styles';
import useMediaQuery from "@material-ui/core/useMediaQuery";
import ListItem from '@material-ui/core/ListItem';
import { useDeps } from "../../../context/DepsContext/DepsContext";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonLinkParent: {
      right: 0,
    },
    gridItem: {
      [theme.breakpoints.down('xs')]: {
        paddingTop: theme.spacing(3),
      }
    },
  }),
);

export default function RSSContent(
  {
    entries
  }: {
    entries: RSSFeedEntryInterface[]
  }
) {
  const classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter
  }: {
    dateFormatter: DateFormatterInterface
  } = useDeps();

  if (entries.length > 5) {
    entries.splice(5, entries.length - 5);
  }

  return (
    <React.Fragment>
      {entries.map((entry, index) => {
        return (
          <ListItem key={index}>
            <ListItemText
              primary={entry.title}
              secondary={dateFormatter.onlyDate(new Date(entry.publishingDate))}
              primaryTypographyProps={ isSmall ? {} : { noWrap: true } }
            />
            <ListItemSecondaryAction className={classes.buttonLinkParent}>
              <IconButton target="_blank" href={entry.link}>
                <ArrowRightIcon />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
        );
      })}
    </React.Fragment>
  );
}
