import EsiUniverseNamesInterface from "../../service/Call/UniverseNamesCall/EsiUniverseNamesInterface";

abstract class AbstractRepository {
  protected _pushUnique(inputArray: number[], value: number): number[] {
    if (!inputArray.includes(value)) {
      inputArray.push(value);
    }

    return inputArray;
  }

  protected _orderNamesByID(
    names: EsiUniverseNamesInterface[]
  ): {
    [key: string]: EsiUniverseNamesInterface
  } {
    let orderedNames: {[key: string]: EsiUniverseNamesInterface} = {};
    names.forEach(name => {
      orderedNames[String(name.id)] = name;
    });

    return orderedNames;
  }
}

export default AbstractRepository;
