import { ENQUEUE_SNACKBAR, CLOSE_ONE, CLOSE_ALL, REMOVE_SNACKBAR } from './notifierActions';
import ReduxNotificationInterface from "./ReduxNotificationInterface";
import NotifierStateInterface from "./NotifierStateInterface";

type NotifierAction = {
  type: string,
  notification?: ReduxNotificationInterface,
  key?: number
};

const initialState: NotifierStateInterface = {
  notifications: [],
};

export default function notifierReducer(
  state: NotifierStateInterface = initialState,
  action: NotifierAction
): NotifierStateInterface {
  switch (action.type) {
    case ENQUEUE_SNACKBAR:
      let actionNotification = action.notification as ReduxNotificationInterface;
      return {
        ...state,
        notifications: [
          ...state.notifications,
          {
            ...actionNotification,
          },
        ],
      };
    case CLOSE_ONE:
      return {
        ...state,
        notifications: state.notifications.map(notification => ({
          ...notification,
          dismissed: notification.key === action.key
        })),
      };
    case CLOSE_ALL:
      return {
        ...state,
        notifications: state.notifications.map(notification => ({
          ...notification,
          dismissed: true
        })),
      };
    case REMOVE_SNACKBAR:
      return {
        ...state,
        notifications: state.notifications.filter(
          notification => notification.key !== action.key,
        ),
      };
    default:
      return state;
  }
}
