import CharacterRepositoryInterface from "./CharacterRepositoryInterface";
import RepoCharacterInterface from "./RepoCharacterInterface";
import AncestryRepositoryInterface from "../AncestryRepository/AncestryRepositoryInterface";
import CharacterInfoCallInterface from "../../service/Call/CharacterInfoCall/CharacterInfoCallInterface";
import BloodlineRepositoryInterface from "../BloodlineRepository/BloodlineRepositoryInterface";
import NameRepositoryInterface from "../NameRepository/NameRepositoryInterface";
import RaceRepositoryInterface from "../RaceRepository/RaceRepositoryInterface";
import CharacterCorpHistoryCallInterface
  from "../../service/Call/CharacterCorpHistoryCall/CharacterCorpHistoryCallInterface";
import RepoCharacterCorpHistoryInterface from "./RepoCharacterCorpHistoryInterface";

class CharacterRepository implements CharacterRepositoryInterface {
  private readonly _characterInfoCall;
  private readonly _characterCorpHistoryCall;
  private readonly _ancestryRepo;
  private readonly _bloodlineRepo;
  private readonly _nameRepo;
  private readonly _raceRepo;

  constructor(
    characterInfoCall: CharacterInfoCallInterface,
    characterCorpHistoryCall: CharacterCorpHistoryCallInterface,
    ancestryRepo: AncestryRepositoryInterface,
    bloodlineRepo: BloodlineRepositoryInterface,
    nameRepo: NameRepositoryInterface,
    raceRepo: RaceRepositoryInterface
  ) {
    this._characterInfoCall = characterInfoCall;
    this._characterCorpHistoryCall = characterCorpHistoryCall;
    this._ancestryRepo = ancestryRepo;
    this._bloodlineRepo = bloodlineRepo;
    this._nameRepo = nameRepo;
    this._raceRepo = raceRepo;
  }

  /**
   * @throws Error
   */
  public async getOne(id: number): Promise<RepoCharacterInterface> {
    let esiCharPromise = this._characterInfoCall.get(id);
    let repoCorpHistoryPromise = this._getCorpHistory(id);
    let esiChar = await esiCharPromise;
    let repoChar: RepoCharacterInterface = {
      ...esiChar,
      alliance_name: esiChar.alliance_id === undefined ? undefined : "#error",
      ancestry_name: esiChar.ancestry_id === undefined ? undefined : "#error",
      bloodline_name: "#error",
      corporation_name: "#name",
      faction_name: esiChar.faction_id === undefined ? undefined : "#error",
      race_name: "#error",
      id: id,
      corporation_history: []
    }

    let {
      alliance_id,
      ancestry_id,
      bloodline_id,
      corporation_id,
      faction_id,
      race_id
    } = esiChar;
    let idsToResolve: number[] = [];
    idsToResolve.push(corporation_id);
    if (alliance_id !== undefined) {
      idsToResolve.push(alliance_id);
    }
    if (faction_id !== undefined) {
      idsToResolve.push(faction_id);
    }

    let ancestryPromise = null; // Because ancestry is optional
    if (ancestry_id !== undefined) {
      ancestryPromise = this._ancestryRepo.getName(ancestry_id);
    }
    let bloodlinePromise = this._bloodlineRepo.getName(bloodline_id);
    let namesPromise = this._nameRepo.resolveGeneric(idsToResolve);
    let racePromise = this._raceRepo.getName(race_id);

    if (ancestryPromise !== null) {
      try {
        repoChar.ancestry_name = await ancestryPromise;
      } catch (e) {
        // Intentionally left blank, it's a call of secondary importance
      }
    }
    try {
      repoChar.bloodline_name = await bloodlinePromise;
    } catch (e) {
      // Intentionally left blank, it's a call of secondary importance
    }
    try {
      repoChar.race_name = await racePromise;
    } catch (e) {
      // Intentionally left blank, it's a call of secondary importance
    }
    try {
      let charNames = await namesPromise;
      charNames.forEach(resolvedName => {
        let { id, name } = resolvedName;
        if (id === corporation_id) {
          repoChar.corporation_name = name;
        }
        if (id === alliance_id) {
          repoChar.alliance_name = name;
        }
        if (id === faction_id) {
          repoChar.faction_name = name;
        }
      });
    } catch (e) {
      // Intentionally left blank, it's a call of secondary importance
    }

    repoChar.corporation_history = await repoCorpHistoryPromise;

    return repoChar;
  }

  private async _getCorpHistory(id: number): Promise<RepoCharacterCorpHistoryInterface[]> {
    let corpHistory = await this._characterCorpHistoryCall.get(id);
    let idsToResolve: number[] = [];
    corpHistory.forEach(corp => {
      if (corp.is_deleted === undefined || !corp.is_deleted) {
        idsToResolve = this._pushUnique(idsToResolve, corp.corporation_id);
      }
    });

    let names = await this._nameRepo.resolveGeneric(idsToResolve);
    let repoCorpHistory: RepoCharacterCorpHistoryInterface[] = [];
    corpHistory.forEach(corp => {
      let { corporation_id } = corp;
      let foundName = `#${corporation_id}`;
      for (let name of names) {
        if (name.id === corporation_id) {
          foundName = name.name;
          break;
        }
      }

      repoCorpHistory.push({
        ...corp,
        corporation_name: foundName
      });
    });

    return repoCorpHistory;
  }

  private _pushUnique(inputArray: number[], value: number): number[] {
    if (!inputArray.includes(value)) {
      inputArray.push(value);
    }

    return inputArray;
  }
}

export default CharacterRepository;
