import EntityManagerInterface from "../EntityManagerInterface";
import BaseTableInterface from "./BaseTableInterface";

abstract class BaseTable implements BaseTableInterface {
  abstract name: string;
  private _em: EntityManagerInterface;

  protected constructor(
    em: EntityManagerInterface
  ) {
    this._em = em;
  }

  public async addOne(value: any, key?: any): Promise<void> {
    return this.addMany([{value, key}]);
  }

  public async addMany(items: [{value: any, key?: any}]): Promise<void> {
    let transaction = await this._getReadwriteTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      items.forEach(item => {
        if (item.key === undefined) {
          store.add(item.value);
        } else {
          store.add(item.value, item.key);
        }
      });

      // "oncomplete" fires after all transaction completed successfully,
      // unlike "onsuccess" which fires when some transaction may still fail
      // after the event.
      transaction.oncomplete = () => {
        resolve();
      };

      transaction.onerror = () => {
        reject(this._getErrorMessage(transaction.error));
      };
    });
  }

  public async clear(): Promise<void> {
    let transaction = await this._getReadwriteTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      store.clear();

      transaction.oncomplete = () => {
        resolve();
      }

      transaction.onerror = () => {
        reject(this._getErrorMessage(transaction.error));
      };
    });
  }

  // TODO: also in index
  public async count(key?: IDBValidKey | IDBKeyRange): Promise<number> {
    let transaction = await this._getReadonlyTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      let request = store.count(key);

      request.onsuccess = () => {
        resolve(request.result);
      }

      request.onerror = () => {
        reject(this._getErrorMessage(request.error));
      }
    });
  }

  public async delete(key: IDBValidKey | IDBKeyRange): Promise<void> {
    let transaction = await this._getReadwriteTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      store.delete(key);

      transaction.oncomplete = () => {
        resolve();
      }

      transaction.onerror = () => {
        reject(this._getErrorMessage(transaction.error));
      };
    });
  }

  public async getOne<T>(key: IDBValidKey): Promise<T | undefined> {
    return await this._get(key) as T;
  }

  public async getRange<T>(keyRange: IDBKeyRange): Promise<T[] | undefined> {
    return await this._get(keyRange) as T[];
  }

  // fixme
  public async getAll(): Promise<void> {
    let transaction = await this._getReadonlyTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      store.getAll()
      // fixme continue here
    });
  }

  public async putOne<T>(value: T, key?: IDBValidKey): Promise<void> {
    return this.putMany([{ value, key }]);
  }

  public async putMany<T>(items: [{ value: T, key?: IDBValidKey }]): Promise<void> {
    let transaction = await this._getReadwriteTransaction()

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      items.forEach(item => {
        if (item.key === undefined) {
          store.put(item.value);
        } else {
          store.put(item.value, item.key);
        }
      });

      // "oncomplete" fires after all transaction completed successfully,
      // unlike "onsuccess" which fires when some transaction may still fail
      // after the event.
      transaction.oncomplete = () => {
        resolve();
      };

      transaction.onerror = () => {
        reject(this._getErrorMessage(transaction.error));
      };
    });
  }

  private async _get<T>(query: IDBValidKey | IDBKeyRange): Promise<T|T[]> {
    let transaction = await this._getReadonlyTransaction();

    return new Promise((resolve, reject) => {
      let store = this._getObjectStore(transaction);
      let request = store.get(query);

      request.onsuccess = () => {
        resolve(request.result);
      }

      transaction.onerror = () => {
        reject(this._getErrorMessage(transaction.error));
      };
    });
  }

  private async _getReadonlyTransaction(): Promise<IDBTransaction> {
    let connection = await this._em.getConnection();

    return connection.transaction([this.name], "readonly");
  }

  private async _getReadwriteTransaction(): Promise<IDBTransaction> {
    let connection = await this._em.getConnection();

    return connection.transaction([this.name], "readwrite");
  }

  private _getObjectStore(transaction: IDBTransaction): IDBObjectStore {
    return transaction.objectStore(this.name);
  }

  private _getErrorMessage(error: DOMException | null): string {
    let message = "Database error";
    if (error !== null) {
      message = `Error: ${error.code} ${error.message} ${error.name}`;
    }

    return message;
  }
};

export default BaseTable;
