import Container from "../../service/Container/Container";
import {RootState} from "../../store";

export const THEME_CHANGE_MODE = 'theme/changeMode';
export const THEME_SET_MODE = 'theme/setMode';

export async function changeDarkMode(
  dispatch: any,
  getState: any,
  container: Container
) {
  let state = getState() as RootState;
  let { darkMode }: { darkMode: boolean } = state.theme;
  let { settingsRepository } = container;

  settingsRepository.setDarkMode(!darkMode).catch(e => console.error(e));
  dispatch({ type: THEME_CHANGE_MODE });
}

export function setDarkMode(darkMode: boolean, save: boolean) {
  return function(dispatch: any, getState: any, container: Container) {
    let { settingsRepository } = container;

    if (save) {
      settingsRepository.setDarkMode(darkMode).catch(e => console.error(e));
    }
    dispatch({ type: THEME_SET_MODE, payload: darkMode });
  };
}
