import React from 'react';
import {AutocompleteRenderInputParams} from "@material-ui/lab/Autocomplete/Autocomplete";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";

export default function AutocompleteTextField(
  {
    params,
    searchTextErrorText,
    searchTextIsError,
    submitWithoutOptionHandler
  }: {
    params: AutocompleteRenderInputParams,
    searchTextErrorText: string,
    searchTextIsError: boolean,
    submitWithoutOptionHandler: () => void
  }
) {

  return (
    <TextField
      {...params}
      color="secondary"
      error={searchTextIsError}
      helperText={searchTextErrorText}
      fullWidth
      id="home-search-text"
      label="Search"
      variant="outlined"
      InputProps={{
        ...params.InputProps,
        type: 'search',
        endAdornment: (
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={submitWithoutOptionHandler}
              edge="end"
              style={{ marginRight: "-7px" }}
            >
              <SearchIcon />
            </IconButton>
          </InputAdornment>
        )
      }}
    />
  );
}
