import QueryDictionaryInterface from "./QueryDictionaryInterface";

class Query  {
  private readonly _spaceDelimiter;
  private _query: QueryDictionaryInterface = {};

  constructor(spaceDelimiter: string = ",") {
    this._spaceDelimiter = spaceDelimiter;
  }

  add(attribute: string, values: string[]): void {
    this._query[attribute] = values;
  }

  isEmpty(): boolean {
    return Object.keys(this._query).length === 0;
  }

  toString(): string {
    let queryString = "";

    for (let attribute in this._query) {
      if (!this._query.hasOwnProperty(attribute)) {
        continue;
      }

      if (queryString.length > 0) {
        queryString = queryString.concat("&");
      }

      let values = "";
      this._query[attribute].forEach((value, index) => {
        if (index > 0) {
          values = values.concat(this._spaceDelimiter);
        }

        values = values.concat(encodeURIComponent(value));
      });

      queryString = queryString.concat(`${attribute}=${values}`);
    }

    return queryString;
  }
}

export default Query;
