const initialState = {
  mobileOpen: false,
}

export default function drawerReducer(
  state = initialState,
  action: {type: string, payload?: any}
) {
  switch (action.type) {
    case 'drawer/mobileOpen': {
      return {
        ...state,
        mobileOpen: true,
      };
    }
    case 'drawer/mobileClose': {
      return {
        ...state,
        mobileOpen: false
      };
    }
    default:
      return state;
  }
}
