import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiStatusInterface from "./EsiStatusInterface";
import EsiError from "../../../error/EsiError/EsiError";
import StatusCallInterface from "./StatusCallInterface";

class StatusCall implements StatusCallInterface{
  private readonly _esiClient;
  private readonly _url = "/v2/status/";

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  async get(): Promise<EsiStatusInterface> {
    let response = await this._esiClient.getResponse(this._url, "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }
}

export default StatusCall;
