import AncestryRepositoryInterface from "./AncestryRepositoryInterface";
import EsiAncestryInterface from "../../service/Call/AncestryCall/EsiAncestryInterface";
import AncestryCallInterface from "../../service/Call/AncestryCall/AncestryCallInterface";

class AncestryRepository implements AncestryRepositoryInterface {
  private readonly _ancestryCall;

  constructor(ancestryCall: AncestryCallInterface) {
    this._ancestryCall = ancestryCall;
  }

  /**
   * @throws Error
   */
  public async getName(id: number): Promise<string> {
    let ancestries = await this._ancestryCall.get();
    let foundAncestry: EsiAncestryInterface | null = null;
    for (let ancestry of ancestries) {
      if (ancestry.id === id) {
        foundAncestry = ancestry;
        break;
      }
    }

    if (foundAncestry === null) {
      throw new Error("Ancestry not found");
    }

    return foundAncestry.name;
  }
}

export default AncestryRepository;
