import AllianceInfoCallInterface from "./AllianceInfoCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";
import EsiAllianceInfoInterface from "./EsiAllianceInfoInterface";

class AllianceInfoCall implements AllianceInfoCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  async get(id: number): Promise<EsiAllianceInfoInterface> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v4/alliances/${id}/`;
  }
}

export default AllianceInfoCall;
