import MUIDataTable, {MUIDataTableColumnDef, MUIDataTableOptions} from "mui-datatables";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import React from "react";

const options: MUIDataTableOptions = {
  elevation: 1,
  filter: false,
  fixedHeader: false,
  print: false,
  responsive: "standard",
  rowHover: false,
  rowsPerPageOptions: [],
  selectableRows: "none",
  viewColumns: false,
};

export default function CorpListTable(
  {
    corpList
  }: {
    corpList: { [key:string]: string }
  }
) {
  const columns: MUIDataTableColumnDef[] = [
    {
      name: "name",
      label: "Name",
      options: {
        customHeadRender: () => null
      }
    },
    {
      name: "id",
      label: "ID",
      options: {
        customHeadRender: () => null,
        display: false,
      }
    },
    {
      name: "icon",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = tableMeta.rowData[1];

          return (
            <IconButton
              size="small"
              aria-label="open"
              component={Link}
              to={`/corporation/${id}`}
            >
              <ArrowRightIcon />
            </IconButton>
          );
        },
        download: false,
        setCellProps: () => ({ style: { width: 0, paddingTop: 0, paddingBottom: 0 }}),
      }
    }
  ];

  let data: { name: string, id: number }[] = [];
  for (let corpID in corpList) {
    data.push({ name: corpList[corpID], id: Number(corpID) });
  }

  return(
    <MUIDataTable
      title={"Corporations"}
      data={data}
      columns={columns}
      options={options}
    />
  );
}
