import CorporationAllianceHistoryCallInterface from "./CorporationAllianceHistoryCallInterface";
import EsiCorporationAllianceHistoryInterface from "./EsiCorporationAllianceHistoryInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiError from "../../../error/EsiError/EsiError";

class CorporationAllianceHistoryCall implements CorporationAllianceHistoryCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  public async get(id: number): Promise<EsiCorporationAllianceHistoryInterface[]> {
    let response = await this._esiClient.getResponse(this._getUrl(id), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(id: number): string {
    return `/v3/corporations/${id}/alliancehistory/`;
  }
}

export default CorporationAllianceHistoryCall;
