import BloodlineCallInterface from "./BloodlineCallInterface";
import EsiClientInterface from "../../EsiClient/EsiClientInterface";
import EsiBloodlineInterface from "./EsiBloodlineInterface";
import EsiError from "../../../error/EsiError/EsiError";

class BloodlineCall implements BloodlineCallInterface {
  private readonly _esiClient;

  constructor(esiClient: EsiClientInterface) {
    this._esiClient = esiClient;
  }

  /**
   * @throws Error
   */
  public async get(): Promise<EsiBloodlineInterface[]> {
    let response = await this._esiClient.getResponse(this._getUrl(), "GET");
    if (!response.ok) {
      throw new EsiError(response);
    }

    return response.json();
  }

  private _getUrl(): string {
    return `/v1/universe/bloodlines/`;
  }
}

export default BloodlineCall;
